<template>
    <div>
        <div v-if="baseInfo">
            <h3 class="margin-b-14">贷款基本信息</h3>
            <Form ref="baseInfo" :model="baseInfo" :rules="validateRule" :label-width="120" v-if="baseInfo">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="业务类型" prop="businessType">
                            <i-select type="text"
                                      :disabled="baseInfo['businessTypeConfig'].length==1"
                                      v-model="baseInfo.businessType"
                                      v="baseInfo.orderApplicationType"
                                      @on-change="val=>{ handleChange(val,'baseInfo.orderApplicationType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['businessTypeConfig']" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="租赁属性" prop="leaseType">
                            <i-select type="text"
                                      :disabled="baseInfo['leaseTypeConfig'].length==1"
                                      v-model="baseInfo.leaseType"
                                      v="baseInfo.leaseType"
                                      @on-change="val=>{ handleChange(val,'baseInfo.leaseType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['leaseTypeConfig']" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="还款方式" prop="repaymentMode">
                            <i-select type="text"
                                      :disabled="baseInfo['repaymentModeConfig'].length==1"
                                      v-model="baseInfo.repaymentMode"
                                      v="baseInfo.repaymentMode"
                                      @on-change="val=>{ handleChange(val,'baseInfo.repaymentMode')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['repaymentModeConfig']" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="还款频率" prop="repaymentFrequency">
                            <i-select type="text"
                                      :disabled="baseInfo['repaymentFrequencyConfig'].length==1"
                                      v-model="baseInfo.repaymentFrequency"
                                      v="baseInfo.repaymentFrequency"
                                      @on-change="val=>{ handleChange(val,'baseInfo.repaymentFrequency')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['repaymentFrequencyConfig']"
                                          :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="融资期限" prop="financePeriod">
                            <i-select type="text"
                                      :disabled="baseInfo['financePeriodConfig'].length==1"
                                      v-model="baseInfo.financePeriod"
                                      v="baseInfo.financePeriod"
                                      @on-change="val=>{ handleChange(val,'baseInfo.financePeriod')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['financePeriodConfig']" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否资源车" prop="isResourceCar">
                            <i-select type="text"
                                      v-model.sync="baseInfo.isResourceCar"
                                      :disabled="baseInfo['isResourceCarConfig'].length==1"
                                      v="baseInfo.isResourceCar"
                                      @on-change="val=>{ handleChange(val,'baseInfo.isResourceCar')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['whether']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>

                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="车辆类型" prop="carType">
                            <i-select type="text"
                                      :disabled="baseInfo['carTypeConfig'].length==1"
                                      v-model="baseInfo.carType"
                                      v="baseInfo.carType"
                                      @on-change="val=>{ handleChange(val,'baseInfo.carType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo['carTypeConfig']" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="新车指导价(元)" prop="carGuidancePrice">
                            <InputNumber disabled v-model="baseInfo.carGuidancePrice" placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>

                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="!isSecondhand">
                        <FormItem label="车辆销售价(元)" prop="actualSellingPrice" :rules="createSellPriceRule('车辆销售价')">
                            <InputNumber clearable
                                         v-model="baseInfo.actualSellingPrice"
                                         v="baseInfo.actualSellingPrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>


                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="购车目的" prop="purchasePurpose">
                            <i-select type="text"
                                      v-model="baseInfo.purchasePurpose"
                                      v="baseInfo.purchasePurpose"
                                      @on-change="val=>{ handleChange(val,'baseInfo.purchasePurpose')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['purchasePurpose']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="车辆使用性质" prop="carCommercialType">
                            <i-select type="text"
                                      v-model="baseInfo.carCommercialType"
                                      v="baseInfo.purchasePurpose"
                                      @on-change="val=>{ handleChange(val,'baseInfo.carCommercialType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['usageNature']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="预计上牌城市" class="hascol-form-item">
                            <region-picker type="2" v-model="baseInfo.predictLicense_Region"
                                           regionlabel="预计上牌" ref="regionRef1"
                                           @on-change="changeRegion('predictLicense','baseInfo.predictLicense_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="客户一级渠道" prop="custPrimaryChannel">
                            <i-select type="text" v-model="baseInfo.custPrimaryChannel"
                                      v="gpsInfo.custPrimaryChannel"
                                      @on-change="val=>{handleChange(val,'baseInfo.custPrimaryChannel')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in baseInfo.custPrimaryChannelConfig" :value="item.value"
                                          :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <!--二手车显示的7个字段 start-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="公里数(万公里)" prop="mileage">
                            <InputNumber clearable
                                         v-model="baseInfo.mileage"
                                         v="baseInfo.mileage"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="车辆预估价(元)" prop="carEstimatedPrice" :rules="createEstimatedPriceRule('车辆预估价')">
                            <InputNumber clearable
                                         v-model="baseInfo.carEstimatedPrice"
                                         v="baseInfo.carEstimatedPrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="车架号(VIN码)" prop="vinNum">
                            <Input v-model="baseInfo.vinNum"
                                   :clearable="!nodeFieldDisabledFlag"
                                   :disabled="nodeFieldDisabledFlag"
                                   v="baseInfo.vinNum"
                                   @on-blur="handleBlur"
                                   @on-clear="handleBlur"
                                   placeholder="请输入"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="出厂日期" prop="productionDate" :rules="createProductionDateRule()">
                            <DatePicker type="date" :editable="false"
                                        :disabled="nodeFieldDisabledFlag"
                                        :options="currentDateOptions"
                                        v-model="baseInfo.productionDate"
                                        v="baseInfo.productionDate"
                                        @on-change="val=>{ handleChange(val,'baseInfo.productionDate')}"
                                        placeholder="请选择"></DatePicker>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="初登日期" prop="initialDate" :rules="createInitialDateRule()">
                            <DatePicker type="date" :editable="false"
                                        :options="currentDateOptions"
                                        v-model="baseInfo.initialDate"
                                        v="baseInfo.initialDate"
                                        @on-change="val=>{ handleChange(val,'baseInfo.initialDate')}"
                                        placeholder="请选择"></DatePicker>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="当前牌照" prop="licenseNum" class="hascol-form-item">
                            <Row :gutter="0" class="font0">
                                <i-col :xs="8">
                                    <Form-item prop="provinceCode">
                                        <i-select type="text"
                                                  v-model="baseInfo.provinceCode"
                                                  v="baseInfo.provinceCode"
                                                  @on-change="val=>{ handleChange(val,'baseInfo.provinceCode')}"
                                                  placeholder="请选择">
                                            <i-option v-for="(item,index) in dp['province_shot_name']"
                                                      :value="item.dictCode"
                                                      :key="item.dictCode">
                                                {{item.dictName}}
                                            </i-option>
                                        </i-select>
                                    </Form-item>
                                </i-col>
                                <i-col :xs="8">
                                    <Form-item prop="cityCode">
                                        <i-select type="text" v-model="baseInfo.cityCode"
                                                  v="baseInfo.cityCode"
                                                  @on-change="val=>{ handleChange(val,'baseInfo.cityCode')}"
                                                  placeholder="请选择">
                                            <i-option v-for="(item,index) in DICTLIST_car_number_prefix
" :value="item.dictCode"
                                                      :key="item.dictCode">
                                                {{item.dictName}}
                                            </i-option>
                                        </i-select>
                                    </Form-item>
                                </i-col>
                                <i-col :xs="8">
                                    <Form-item prop="licenseSequence">
                                        <Input clearable v-model="baseInfo.licenseSequence"
                                               v="baseInfo.licenseSequence"
                                               @on-blur="handleBlur"
                                               @on-clear="handleBlur"
                                               placeholder="请输入"></Input>
                                    </Form-item>
                                </i-col>
                            </Row>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="二手车评估机构" prop="carEvaluationAgency">
                            <i-select type="text"
                                      v-model="baseInfo.carEvaluationAgency"
                                      v="baseInfo.carEvaluationAgency"
                                      @on-change="val=>{ handleChange(val,'baseInfo.carEvaluationAgency')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in DICTLIST_carEvaluationAgencyDictList"
                                          :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <!--二手车显示的7个字段 end-->
                    <!--车抵贷（必为二手车）时显示的2个字段 start-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isChedidai">
                        <FormItem label="车辆过户次数" prop="transferTimes">
                            <InputNumber clearable :defaultFormatter="false"
                                         v-model="baseInfo.transferTimes"
                                         v="baseInfo.transferTimes"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isChedidai">
                        <FormItem label="交强险失效日" prop="compulsoryInsuranceExpiryDate">
                            <DatePicker type="date" :editable="false"
                                        v-model="baseInfo.compulsoryInsuranceExpiryDate"
                                        v="baseInfo.compulsoryInsuranceExpiryDate"
                                        @on-change="val=>{ handleChange(val,'baseInfo.compulsoryInsuranceExpiryDate')}"
                                        placeholder="请选择"></DatePicker>
                        </FormItem>
                    </i-col>
                    <!--车抵贷（必为二手车）时显示的2个字段 end-->

                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否反贷" prop="isReverseMortgage">
                            <i-select type="text"
                                      v-model="baseInfo.isReverseMortgage"
                                      v="baseInfo.isReverseMortgage"
                                      @on-change="val=>{ handleChange(val,'baseInfo.isReverseMortgage')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['whether']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <!--新车且反贷时显示-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isReverseMortgage && !isSecondhand">
                        <FormItem label="车辆开票日期" prop="carInvoiceDate">
                            <DatePicker type="date" :editable="false"
                                        :options="currentDateOptions"
                                        v-model="baseInfo.carInvoiceDate"
                                        v="baseInfo.carInvoiceDate"
                                        @on-change="val=>{ handleChange(val,'baseInfo.carInvoiceDate')}"
                                        placeholder="请选择"></DatePicker>
                        </FormItem>
                    </i-col>
                    <!--二手车且反贷时显示-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isReverseMortgage && isSecondhand">
                        <FormItem label="车辆过户完成日期" prop="carTransferDate">
                            <DatePicker type="date" :editable="false"
                                        :options="currentDateOptions"
                                        v-model="baseInfo.carTransferDate"
                                        v="baseInfo.carTransferDate"
                                        @on-change="val=>{ handleChange(val,'baseInfo.carTransferDate')}"
                                        placeholder="请选择"></DatePicker>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 贷款基本信息
     */
    import base from './js/loanbase';
    import rules from './js/loanRules';
    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub, stringToNumber} from '@/utils/helper.js';

    export default {
        mixins: [base],
        name: 'orderLoanBasicForm',
        props: {},
        components: {},
        data() {
            return {
                validateRule: rules.loanBasicRule,
                //日期禁止选择当前日期之前
                currentDateOptions: {
                    disabledDate(date) {
                        return date && date.valueOf() > new Date(new Date().format('yyyy-MM-dd')).getTime();
                    }
                },
                //订单状态节点对应禁用字段：只针对车架号和出厂日期
                nodeFieldDisabledFlag: false
            }
        },
        computed: {
            //牌照第二位过滤生成
            DICTLIST_car_number_prefix() {
                return this.dp['car_number_prefix'].filter(item => item.parentCode == this.baseInfo.provinceCode);
            },
            //二手车评估机构
            DICTLIST_carEvaluationAgencyDictList() {
                if (this.isChedidai)
                    return this.carEvaluationAgencyDictList.filter(item => item.dictCode !== '0');
                else
                    return this.carEvaluationAgencyDictList;
            },
        },
        mounted() {
            this.queryDictData(['whether', 'purchasePurpose', 'usageNature', 'province_shot_name', 'car_number_prefix']);
            //订单状态节点对应禁用字段，为0时禁用
            this.nodeFieldDisabledFlag = this.orderData.orderinfo.editCarFlag === '0';
            //有配置二手车时才去查询二手车评估机构
            if (!!this.baseInfo && !!this.baseInfo.carTypeConfig) {
                var secondObj = this.baseInfo.carTypeConfig.find(item => item.value === '049500001')
                if (secondObj) this.queryEvalAgencyDictList();
            }//查询二手车评估机构
            //贷款基本信息是在loan模块初始时查询的   统计一次
            this.$nextTick(() => {
                this.updateLoanNextDisabled();
                this.todoRecount();
            })
        },
        methods: {
            //实际销售价不得大于新车指导价的1.05倍
            createSellPriceRule(text) {
                return [{
                    validator: (rule, value, callback) => {
                        if (yx.isEmpty(value)) {
                            callback(new Error(text + '不能为空'));
                        } else if (/^((\d+\d*)|(\d+\d*\.\d{1,2}))$/.test(value)) {
                            var maxnum = floatMul(stringToNumber(this.baseInfo.carGuidancePrice || 0), 1.05);
                            if (stringToNumber(value) == 0)
                                callback(new Error(text + '必须大于0！'));
                            else if (stringToNumber(value) > maxnum) {
                                callback(new Error(text + '只能填写0-' + maxnum)); //'不得大于新车指导价的1.05倍'));
                            }
                            else callback();
                        } else {
                            callback(new Error(text + '填写错误'));
                        }
                    },
                    trigger: 'change'
                }]
            },
            //二手车评估价不得大于新车指导价
            createEstimatedPriceRule(text) {
                return [{
                    validator: (rule, value, callback) => {
                        if (yx.isEmpty(value)) {
                            callback(new Error(text + '不能为空'));
                        } else if (/^((\d+\d*)|(\d+\d*\.\d{1,2}))$/.test(value)) {
                            var maxnum = stringToNumber(this.baseInfo.carGuidancePrice || 0);
                            if (stringToNumber(value) == 0)
                                callback(new Error(text + '必须大于0！'));
                            else if (stringToNumber(value) > maxnum) {
                                callback(new Error(text + '只能填写0-' + maxnum));
                            }
                            else callback();
                        } else {
                            callback(new Error(text + '填写错误'));
                        }
                    },
                    trigger: 'change'
                }]
            },
            //出厂日期校验：生成出厂日期不得大于初登日期的校验
            createProductionDateRule() {
                return [{
                    validator: (rule, value, callback) => {
                        var startDate = this.baseInfo.productionDate;
                        var endDate = this.baseInfo.initialDate;
                        if (yx.isEmpty(startDate)) {
                            callback(new Error('出厂日期不得为空'))
                        } else if (!!startDate && !!endDate && new Date(startDate).getTime() > new Date(endDate).getTime()) {
                            callback(new Error('出厂日期不得大于初登日期'))
                        } else {
                            callback();
                        }
                    },
                    trigger: 'change'
                }]
            },
            //初登日期校验：生成出厂日期不得大于初登日期的校验
            createInitialDateRule() {
                return [{
                    validator: (rule, value, callback) => {
                        var startDate = this.baseInfo.productionDate;
                        var endDate = this.baseInfo.initialDate;
                        if (yx.isEmpty(endDate)) {
                            callback(new Error('初登日期不得为空'))
                        } else if (!!startDate && !!endDate && new Date(startDate).getTime() > new Date(endDate).getTime()) {
                            callback(new Error('出厂日期不得大于初登日期'))
                        } else {
                            callback();
                        }
                    },
                    trigger: 'change'
                }]
            },
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;

                const field = handleField.split('.')[1]
                let dom = this.$refs.baseInfo.fields.find(item => item.prop == field);
                //价格改变时通知父组件，触发相关联模块的更新
                if (handleField == 'baseInfo.actualSellingPrice' || handleField == 'baseInfo.carEstimatedPrice') {
                    this.$emit('on-sellpricechange')
                }
                this.$nextTick(() => {
                    this.updateLoanNextDisabled();
                    this.cacheData('baseInfo', handleField);
                    this.todoRecount();
                })
            },
            //下拉框改变值， regionChange为true时val不代表值，而是字段名
            handleChange(val, handleField, regionChange) {
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;

                const field = handleField.split('.')[1];
                let dom = this.$refs.baseInfo.fields.find(item => item.prop == field);
                if (handleField == 'baseInfo.financePeriod') {
                    this.$emit('on-financeperiodchange')
                }

                this.$nextTick(() => {
                    //日期组件有延迟，放在这里进行另一字段的校验
                    if (handleField == "baseInfo.productionDate" && this.baseInfo.initialDate) {
                        this.$refs['baseInfo'].validateField('initialDate')
                    }
                    if (handleField == "baseInfo.initialDate" && this.baseInfo.productionDate) {
                        this.$refs['baseInfo'].validateField('productionDate')
                    }
                    this.updateLoanNextDisabled();
                    this.cacheData('baseInfo', handleField);
                    this.todoRecount();
                })
            },
            //地区改变选择时，store赋值给有效字段,handleField是处理change时字段
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'baseInfo',
                    field: field,
                })
                this.handleChange(field, handleField, true)//第三个参数代表地区值改变
            },
            //校验当前表单，可由父页面调用
            validate(cb) {
                let refName = ['baseInfo', 'regionRef1'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },
        },
        watch: {}
    }
</script>



