<template>
    <div class="apply-loan-module">
        <!--贷款基本信息表单-->
        <order-loan-base-form ref="baseInfoRef" class="cm-panel" v-if="hasBaseInfo"
                              @on-sellpricechange="sellPriceChangeFn"
                              @on-financeperiodchange="financePeriodChangeFn"></order-loan-base-form>
        <div :class="loanNextDisabled?'mask-disabled':''">
            <div class="mask-disabled-content" v-if="loanNextDisabled">
                <div class="mask-disabled-body">
                    <div class="ivu-modal-confirm-head">
                        <div class="ivu-modal-confirm-head-icon ivu-modal-confirm-head-icon-confirm"><i
                                class="ivu-icon ivu-icon-md-alert"></i></div>
                        <div class="ivu-modal-confirm-head-title">提示</div>
                    </div>
                    <div class="ivu-modal-confirm-body">
                        <div>请先完善贷款基本信息。</div>
                    </div>
                </div>
            </div>
            <!--贷款-保险信息表单  包括保险/GPS/盗抢责任险/其他融资项-->
            <order-loan-item-form ref="itemInfoRef"
                                  v-if="hasBaseInfo && hasItemInfo"></order-loan-item-form>

            <!--贷款-贴息信息表单-->
            <order-loan-discount-form ref="discountInfoRef"
                                      v-if="hasBaseInfo && hasDiscountInfo"></order-loan-discount-form>
            <!--&& orderData.loan.baseInfo.hasDiscount == '1'-->

            <!--贷款-保证金信息表单-->
            <order-loan-margin-form ref="marginInfoRef"
                                    v-if="hasBaseInfo && hasMarginInfo"></order-loan-margin-form>

            <!--贷款-贷款试算息表单-->
            <order-loan-calc-form ref="calcInfoRef"
                                  v-if="hasBaseInfo"></order-loan-calc-form>

            <!--试算按钮和结果-->
            <div class="cm-panel" style="margin-top: -34px;">
                <div class="text-center">
                    <Button type="primary" @click="todoCalc" custom-icon="iconfont icon-calculator">贷款试算</Button>
                    <!--<Button type="default" @click="handleReset">重置</Button>-->
                </div>
                <i-table class="table-only-horizon-line margin-t-20" :total="calcres.total"
                         :loading="calcres.loading"
                         border :columns="calcres.columns" :data="calcres.data" v-if="showCalcRes"></i-table>
            </div>
        </div>


    </div>
</template>
<script>
    /**
     * @Description: 贷款信息标签页
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import orderLoanBaseForm from "./baseInfo.vue";
    import orderLoanItemForm from "./itemInfo.vue";
    import orderLoanDiscountForm from "./discountInfo.vue";
    import orderLoanMarginForm from "./marginInfo.vue";
    import orderLoanCalcForm from "./calcInfo.vue";

    import base from './js/loanbase';
    import {validateRules} from '@/utils/validateRules';
    import {yx, scrollTo, stringToNumber} from "@/utils/helper";
    import vFilter from '@/utils/filters';

    export default {
        name: 'orderLoanModule',
        mixins: [base],
        components: {
            orderLoanBaseForm,
            orderLoanItemForm,
            orderLoanDiscountForm,
            orderLoanMarginForm,
            orderLoanCalcForm,
        },
        directives: {},
        props: {},
        data() {
            return {
                contactorModel: {},
                ruleValidate: validateRules.loanInfoPage,
                hasBaseInfo: false,//基本信息加载完毕
                hasItemInfo: false,//融资项信息加载完毕
                hasDiscountInfo: false,//贴息信息
                hasMarginInfo: false,//保证金信息加载完毕
                hasCalcInfo: false,//试算信息加载完毕
                //贷款试算表中数据
                showCalcRes: false,
                calcres: {
                    title: '贷款试算结果',
                    showModal: false,
                    exportLoading: false,
                    loading: false,
                    total: 0,
                    data: [],
                    columns: [
                        {
                            title: '金融机构',
                            key: 'capitalName',
                            render: (h, params) => {
                                return h('div', params.row.capitalName || '--')
                            },
                        },
                        {
                            title: '客户融资额(元)',
                            key: 'custFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custFinanceAmount) || '--')
                            },
                        },
                        {
                            title: '风控融资额(元)',
                            key: 'riskControlFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.riskControlFinanceAmount) || '--')
                            },
                        },
                        {
                            title: '风险融资额(元)',
                            key: 'riskFinanceAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.riskFinanceAmount) || '--')
                            }
                        },
                        {
                            title: '客户利率(%)',
                            key: 'custInterestRate',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custInterestRate) || '--')
                            }
                        },
                        {
                            title: '客户费率(%)',
                            key: 'custCostRate',
                            render: (h, params) => {
                                var rate = params.row.custCostRate;
                                return h('span', {
                                    style: {
                                        'color': rate < 0 ? '#F5222D ' : ''
                                    }
                                }, vFilter.number(rate) || '--')
                            }
                        },
                        {
                            title: '月供金额(元)',
                            key: 'custMonthlyAmount',
                            render: (h, params) => {
                                return h('div', vFilter.number(params.row.custMonthlyAmount) || '--')
                            }
                        },
                        {
                            title: '放款金额(元)',
                            key: 'loanPayableAmount',
                            render: (h, params) => {
                                //自营时显示放款金额，其他资方计算不准备暂不显示
                                if (params.row.capitalCode == 'YX')
                                    return h('div', vFilter.number(params.row.loanPayableAmount) || '--')
                                else
                                    return h('div', '--')
                            }
                        },
                    ]
                },
            }
        },
        computed: {
            ...mapState('apply', [
                'orderData',
            ]),
        },
        mounted() {
            this.queryDictData(['province']).then(dp => {
                if (!dp.province || !dp.province.length) {
                    this.errorModal('请求省份数据为空');
                }
            });
            //初始查询信息
            this.queryInitData('baseInfo', row => {
                //贷款基本信息
                this.hasBaseInfo = !!row;
                this.hasItemInfo = row.hasInsurance == '1' || row.hasGps == '1'
                    || row.hasTheftInsurance == '1' || row.hasOtherFinance == '1';
                this.hasMarginInfo = row.hasMargin == '1';
                //费率产品和息费产品时要显示手续费的两个字段
                const isFeeProd = row.chargingMode == "011100001" || row.chargingMode == "011100002";
                this.hasDiscountInfo = isFeeProd || row.hasFeeDiscount == '1' || row.hasRateDiscount == '1';
            })
        },
        destroyed() {
            sessionStorage.removeItem('loan'); //页面销毁时清除loan缓存
        },
        methods: {
            ...mapMutations('apply', [
                'setLoanData'
            ]),
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
            ]),
            //贷款基本信息中融资期限时
            financePeriodChangeFn() {
                if (this.orderData.loan.itemInfo && this.orderData.loan.itemInfo.theftInsuranceInfo && this.$refs['itemInfoRef'])
                    this.$refs['itemInfoRef'].updateTheftBasePrice()
                if ((this.baseInfo.isShowPoundage == 1 || this.baseInfo.hasFeeDiscount == 1 || this.baseInfo.hasRateDiscount == 1) && this.$refs['discountInfoRef']) {
                    //手续费和贴息重新请求,加上重载标记
                    this.$refs['discountInfoRef'].reloadDiscountInfo(true)
                }
            },
            //贷款基本信息中新车销售价或二手车预估价修改时
            sellPriceChangeFn() {
                if (this.orderData.loan.itemInfo && this.orderData.loan.itemInfo.theftInsuranceInfo && this.$refs['itemInfoRef'])
                    this.$refs['itemInfoRef'].updateTheftBasePrice()
                if (this.orderData.loan.marginInfo && this.$refs['marginInfoRef']) {
                    //重新计算保证金
                    this.$refs['marginInfoRef'].updateMarginCalc()
                }
                if (this.orderData.loan.calcInfo && this.$refs['calcInfoRef']) {
                    //根据尾付比例计算尾付金额
                    this.$refs['calcInfoRef'].updateCalcAmount()
                }
            },
            //试算后回塞值
            updateFullLoanData(obj) {
                console.log(obj)
                //试算结果
                this.calcres.data.splice(0);
                if (!obj) return false;
                var financeCalculationInfo = obj.financeCalculationInfo;
                if (!!financeCalculationInfo && !!financeCalculationInfo.financeCalculationTableList) {
                    var numFields = ['custCostRate', 'custFinanceAmount', 'custInterestRate', 'custMonthlyAmount', 'loanPayableAmount', 'riskControlFinanceAmount', 'riskFinanceAmount']
                    financeCalculationInfo.financeCalculationTableList.forEach(item => {
                        numFields.forEach((field) => {
                            item[field] = '' + stringToNumber(item[field])
                        })
                        this.calcres.data.push(item)
                    })
                    this.showCalcRes = true;
                }
                this.orderData.loan.calcInfo.financeCalculationTableList = this.calcres.data;

                //保险
                var itemInfo = this.orderData.loan.itemInfo;
                var newItemInfo = obj.financeItemInfo;
                if (!!itemInfo && !!newItemInfo) {
                    if (!!itemInfo.insuranceInfo && !!itemInfo.insuranceInfo.insuranceItemList &&
                        !!newItemInfo.insuranceInfo && !!newItemInfo.insuranceInfo.insuranceItemList) {
                        itemInfo.insuranceInfo.insuranceItemList.forEach((item, index) => {
                            var newItem = newItemInfo.insuranceInfo.insuranceItemList[index];
                            var insArr = ["insurancMinPrice", "insuranceMaxPrice", "insuranceAmount"];
                            insArr.forEach(field => {
                                this.$set(item, field, '' + stringToNumber(newItem[field]));
                            })
                        })
                    }
                    if (!!itemInfo.gpsInfo && !!newItemInfo.gpsInfo) {
                        var gpsArr = ["increasePriceMax", "increasePriceMin"];
                        gpsArr.forEach(field => {
                            this.$set(itemInfo.gpsInfo, field, '' + stringToNumber(newItemInfo.gpsInfo[field]));
                        })
                    }
                    if (!!itemInfo.theftInsuranceInfo && !!newItemInfo.theftInsuranceInfo) {
                        var theftArr = ["basePrice", "increasePrice", "increaseMaxPrice", "increaseMinPrice", "totalPrice"];
                        theftArr.forEach(field => {
                            this.$set(itemInfo.theftInsuranceInfo, field, '' + stringToNumber(newItemInfo.theftInsuranceInfo[field]));
                        })
                    }

                    if (!!itemInfo.otherFinanceInfo && !!newItemInfo.otherFinanceInfo) {
                        itemInfo.otherFinanceInfo.forEach((item, index) => {
                            var newItem = newItemInfo.otherFinanceInfo[index];
                            this.$set(item, "financePrice", '' + stringToNumber(newItem.financePrice));
                        })
                    }
                    if (!this.orderTabs[1].hasSaved) this.cacheData('itemInfo');
                }
                //保证金
                var marginInfo = this.orderData.loan.marginInfo;
                var newMarginInfo = obj.financeMarginInfo;
                if (!!marginInfo && !!newMarginInfo) {
                    this.$set(marginInfo, "marginAmount", '' + stringToNumber(newMarginInfo.marginAmount));
                    this.$set(marginInfo, "marginProportion", '' + stringToNumber(newMarginInfo.marginProportion));
                    if (!this.orderTabs[1].hasSaved) this.cacheData('marginInfo');
                }

                //贴息
                var discountInfo = this.orderData.loan.discountInfo;
                var newDiscountInfo = obj.financeDiscountInfo;
                if (!!discountInfo && !!newDiscountInfo) {
                    if (!!discountInfo.feeDiscountInfo && !!newDiscountInfo.feeDiscountInfo) {
                        this.$set(discountInfo.feeDiscountInfo, "poundageAmount", '' + stringToNumber(newDiscountInfo.feeDiscountInfo.poundageAmount));
                        this.$set(discountInfo.feeDiscountInfo, "dealerDiscountAmount", '' + stringToNumber(newDiscountInfo.feeDiscountInfo.dealerDiscountAmount));
                        this.$set(discountInfo.feeDiscountInfo, "manufacturerDiscountAmount", '' + stringToNumber(newDiscountInfo.feeDiscountInfo.manufacturerDiscountAmount));
                        this.$set(discountInfo.feeDiscountInfo, "discountTotalAmount", '' + stringToNumber(newDiscountInfo.feeDiscountInfo.discountTotalAmount))
                        this.$set(discountInfo.feeDiscountInfo, "ratioValue", '' + stringToNumber(newDiscountInfo.feeDiscountInfo.ratioValue))
                    }
                    if (!!discountInfo.rateDiscountInfo && !!newDiscountInfo.rateDiscountInfo) {
                        this.$set(discountInfo.rateDiscountInfo, "dealerDiscountAmount", '' + stringToNumber(newDiscountInfo.rateDiscountInfo.dealerDiscountAmount));
                        this.$set(discountInfo.rateDiscountInfo, "manufacturerDiscountAmount", '' + stringToNumber(newDiscountInfo.rateDiscountInfo.manufacturerDiscountAmount));
                        this.$set(discountInfo.rateDiscountInfo, "discountTotalAmount", '' + stringToNumber(newDiscountInfo.rateDiscountInfo.discountTotalAmount))
                        this.$set(discountInfo.rateDiscountInfo, "ratioValue", '' + stringToNumber(newDiscountInfo.rateDiscountInfo.ratioValue))
                    }
                    if (!this.orderTabs[1].hasSaved) this.cacheData('discountInfo');
                }

                //计算
                var calcInfo = this.orderData.loan.calcInfo;
                if (!!calcInfo && !!obj.financeCalculationInfo) {
                    var calcArr = ["downPaymentProportion", "downPaymentAmount", "finalPaymentProportion", "finalPaymentAmount"];
                    calcArr.forEach(field => {
                        this.$set(calcInfo, field, '' + stringToNumber(obj.financeCalculationInfo[field]));
                    })
                    if (!this.orderTabs[1].hasSaved) this.cacheData('calcInfo');
                }
            },
            //请求试算
            todoCalc() {
                this.calcres.data.splice(0);
                this.validate(valid => {
                    if (!valid) return this.errorModal('请先完善贷款基本信息之后再进行试算！');
                    let queryParams = this.getSaveData(); //贷款试算需要的数据是所有客户和贷款中要转换成表单的数据
                    let url = polarisserver + '/finance/calculation/financeInfo';
                    this.showLoading();
                    this.$http.post(url, queryParams).then((res) => {
                        this.hideLoading();
                        let result = res.data;
                        //不管成功还是不成功，如果返回data的话都需要更新试算返回的页面结果，查询资方为空的话不会返回data
                        if (!!result.data) {
                            this.updateFullLoanData(result.data)
                            this.$nextTick(()=>{
                                this.todoRecount()
                            })
                        }
                        if (!result.success) {
                            return this.errorModal(result.errorMessage || '请求贷款试算失败');
                        }
                    }).catch((err) => {
                        this.hideLoading();
                        this.errorCatchModal('请求贷款试算出错：', err);
                    })
                })
            },
            //保存全部贷款信息，编辑主页调用保存，后台会自动进行试算，返回结果与试算相同
            toSave(submitFlag) {
                return new Promise((resolve, reject) => {
                    this.validate(valid => {
                        if (!valid) {
                            resolve(false)
                        } else {
                            this.showLoading();
                            let saveData = this.getSaveData();
                            saveData.saveFrom = 'PC';
                            let url = polarisserver + "/finance/save/financeInfo";
                            this.$http.post(url, saveData).then((res) => {
                                this.hideLoading();
                                let result = res.data;
                                this.setOrderLoanTabSaved(result.success);//修改保存状态
                                if (!!result.data) {
                                    //需要更新试算返回的页面结果
                                    this.updateFullLoanData(result.data);
                                    this.$nextTick(()=>{
                                        this.todoRecount()
                                    })
                                }
                                if (!result.success) {
                                    this.errorModal(result.errorMessage || '贷款信息保存失败');
                                    resolve(false)
                                } else {
                                    //提交标识，提交前调用的话不用提示
                                    if (!submitFlag) this.$Message.success('贷款信息保存成功!')
                                    resolve(true)
                                }
                                //resolve(result);
                            }).catch((err) => {
                                this.hideLoading();
                                this.setOrderLoanTabSaved(false)
                                this.errorCatchModal('贷款信息保存请求出错：', err);
                                reject(err)
                            })
                        }
                    })
                })
            },
            scrollToValid(flag) {
                this.$nextTick(() => {
                    if (!flag) {
                        let el = $('.apply-loan-module .ivu-form-item-error')[0];
                        if (el && el.getBoundingClientRect) {
                            let posObj = el.getBoundingClientRect();//top/right/bottom/left
                            let st = document.documentElement.scrollTop || document.body.scrollTop;
                            let total = posObj.top + st - 200;
                            //滚定到位置
                            scrollTo(document, total, 300);
                        }
                    }
                    this.setOrderModuleError({index: 1, hasError: !flag})
                })
            },
            //贷款信息全部校验，回传给父级页
            validate(cb) {
                let refName = ['baseInfoRef', 'itemInfoRef', 'marginInfoRef', 'discountInfoRef', 'calcInfoRef'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    this.scrollToValid(flag);
                    cb && cb(flag);
                });
            }
        },
    }
</script>



