<template>
    <div>
        <div class="cm-panel" v-if="marginInfo">
            <h3 class="margin-b-14">保证金信息</h3>
            <Form ref="marginInfo" :model="marginInfo" :rules="rules.loanDepositRule" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="保证金金额" prop="marginAmount" :rules="createMarginAmountRule()">
                            <InputNumber v-model="marginInfo.marginAmount"
                                         :placeholder1="isFixedRatio?'计算后带出':isEditable?'请输入':''"
                                         :placeholder="marginAmountDisabled?'计算后带出':'请输入'"
                                         :clearable="isEditable"
                                         :disabled="marginAmountDisabled"
                                         :disabled1="!isEditable"
                                         v="marginInfo.marginAmount"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="保证金(%)" prop="marginProportion" :rules="createMarginRatioRule()">
                            <InputNumber v-model="marginInfo.marginProportion"
                                         :placeholder1="isFixedAmount?'计算后带出':isEditable?'请输入':''"
                                         :placeholder="marginPctDisabled?'计算后带出':'请输入'"
                                         :clearable="isEditable"
                                         :disabled="marginPctDisabled"
                                         :disabled1="!isEditable"
                                         v="marginInfo.marginProportion"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="保证金是否冲抵" prop="marginIsOffset">
                            <RadioGroup v-model="marginInfo.marginIsOffset"
                                        v="marginInfo.marginIsOffset">
                                <Radio v-for="(item,index) in DICT_marginIsOffset" :label="item.dictCode"
                                       disabled
                                       :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 保证金
     */

    import base from './js/loanbase';
    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub, stringToNumber} from '@/utils/helper.js';
    import {amountValidator, pctValidator} from '@/utils/validateRules.js';

    export default {
        name: 'marginInfo',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                isFixedAmount: false,
                isFixedRatio: false,
                isEditable: false,
                marginMethod: 0,//转换后的保证金类型
                DICT_marginIsOffset: []
            }
        },
        computed: {
            marginAmountDisabled() {
                if (this.marginMethod != '3') return true;
                var value1 = this.marginInfo.marginAmount,
                    value2 = this.marginInfo.marginProportion;
                return (value1 === '' || value1 === null) && !(value2 === '' || value2 === null)
            },
            marginPctDisabled() {
                if (this.marginMethod != '3') return true;
                var value1 = this.marginInfo.marginAmount,
                    value2 = this.marginInfo.marginProportion;
                return !(value1 === '' || value1 === null) && (value2 === '' || value2 === null)
            },
        },
        mounted() {
            this.queryDictData(['whether']);
            if (this.baseInfo.hasMargin == '1') {
                this.queryInitData('marginInfo', () => {
                    //转换后的类型1固定金额，2固定比例，3自定义  配置的保证金类型(保证金计算方式)
                    this.marginMethod = yx.getMarginMethod(this.marginInfo.marginType);
                    this.isFixedAmount = this.marginMethod == '1';
                    this.isFixedRatio = this.marginMethod == '2';
                    this.isEditable = this.marginMethod == '3';
                    this.DICT_marginIsOffset = this.dp['whether'];
                    //初始如果是比例或金额时且销售价有值时先计算一次
                    if ((this.marginMethod == '1' || this.marginMethod == '2') && this.sellingAmount !== '') {
                        this.updateMarginCalc();
                    }
                    this.$nextTick(() => {
                        this.todoRecount();
                    })
                })
            }
        },
        methods: {
            //生成保证金金额校验，有保证金则代表校验必填，自定义才能校，固定金额和固定比例要等试算结束
            createMarginAmountRule() {
                let rule = [];
                //rule.push(amountValidator('保证金金额', this.isEditable))
                rule = [
                    {
                        validator: (rule, value, callback) => {
                            var num1 = value;
                            var num2 = this.marginInfo.marginProportion;
                            if (this.marginMethod == '3' && yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                callback(new Error('保证金金额和保证金比例必须填一项'));
                            } else if ((this.marginMethod == '2' || this.marginMethod == '3') && !yx.isEmpty(num2) && yx.isEmpty(num1)) {
                                callback();
                            } else if (!(yx.amountReg.test(value))) {
                                callback(new Error('保证金金额格式不对'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'change',
                    },
                ]
                return rule;
            },
            //生成保证金比例校验
            createMarginRatioRule() {
                let rule = [];
                //rule.push(pctValidator('保证金比例', this.isEditable))
                rule = [
                    {
                        validator: (rule, value, callback) => {
                            var num1 = this.marginInfo.marginAmount;
                            var num2 = value;
                            if (this.marginMethod == '3' && yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                callback(new Error('保证金金额和保证金比例必须填一项'));
                            } else if ((this.marginMethod == '1' || this.marginMethod == '3') && !yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                callback();
                            } else if (!(yx.pctReg.test(value))) {
                                callback(new Error('保证金比例只能输入0 - 100'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'change',
                    },
                ]
                return rule;
            },
            //根据保证金类型重新计算   可以根据车款直接计算  APP不计算
            updateMarginCalc(isAmount) {
                if ((isAmount && this.isEditable) || this.isFixedAmount) {
                    //根据保证金金额算出保证金比例
                    let price = stringToNumber(this.marginInfo.marginAmount || 0);
                    if (price == 0 || this.sellingAmount == 0) {
                        this.marginInfo.marginProportion = 0;
                        return;
                    }
                    let num = floatDiv(floatMul(price, 100), this.sellingAmount);
                    this.marginInfo.marginProportion = stringToNumber(num.toFixed(2));
                    this.$refs['marginInfo'] && this.$refs['marginInfo'].validateField('marginProportion');
                } else if (this.isEditable || this.isFixedRatio) {
                    //默认根据保证金比例算出保证金金额
                    let ratio = stringToNumber(this.marginInfo.marginProportion || 0);
                    if (ratio == 0 || this.sellingAmount == 0) {
                        this.marginInfo.marginAmount = 0;
                        return;
                    }
                    let num = floatMul(floatDiv(ratio, 100), this.sellingAmount);
                    this.marginInfo.marginAmount = stringToNumber(num.toFixed(2));
                    this.$refs['marginInfo'] && this.$refs['marginInfo'].validateField('marginAmount');
                }
            },
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                /*----以下为计算 start----*/
                //根据保证金金额算出保证金比例
                if (handleField == 'marginInfo.marginAmount') {
                    this.updateMarginCalc(true)
                }
                //根据保证金比例算出保证金金额
                if (this.isEditable && handleField == 'marginInfo.marginProportion') {
                    this.updateMarginCalc()
                }
                /*----以上为计算 end----*/
                /*----以下为非计算 start----*/
                /*//自定义时根据保证金金额清空首保证金比例
                if (handleField == 'marginInfo.marginAmount' && this.marginMethod == '3') {
                    let value1 = this.marginInfo.marginAmount;
                    let value2 = this.marginInfo.marginProportion;
                    if (!yx.isEmpty(value1) && !yx.isEmpty(value2)) {
                        this.marginInfo.marginProportion = "";
                    }
                    this.$refs['marginInfo'].validateField('marginProportion');
                }
                //自定义时根据保证金比例清空保证金金额
                if (handleField == 'marginInfo.marginProportion' && this.marginMethod == '3') {
                    let value1 = this.marginInfo.marginAmount;
                    let value2 = this.marginInfo.marginProportion;
                    if (!yx.isEmpty(value1) && !yx.isEmpty(value2)) {
                        this.marginInfo.marginAmount = "";
                    }
                    this.$refs['marginInfo'].validateField('marginAmount');
                }*/
                /*----以上为非计算 end----*/
                this.$nextTick(() => {
                    this.cacheData('marginInfo');
                    this.todoRecount();
                })
            },
            //下拉框改变值 保证金冲抵只有一项，所以没有change
            handleChange(val, handleField) {
                this.$nextTick(() => {
                    this.cacheData('marginInfo');
                    this.todoRecount();
                })
            },

            //校验当前表单，可由父页面调用
            validate(cb) {
                if (this.$refs['marginInfo']) this.$refs['marginInfo'].validate((valid) => {
                    cb && cb(valid);
                });
                else cb && cb(true);
            },
        },
    }
</script>



