<template>
    <div class="insurance-drop-panel">
        <div class="insurance-drop-header" @click="handleCheckAll">
            <Checkbox :indeterminate="indeterminate" :value="checkAll"></Checkbox>
            <span class="insurance-drop-alltext">全选</span>
        </div>
        <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange" style="height:128px;overflow:auto">
            <label class="ivu-select-item" style="display:block;" v-for="(item,index) in insTypesDict"
                   :key="index">
                <Checkbox :label="item.dictCode">{{item.dictName}}</Checkbox>
            </label>
        </CheckboxGroup>
    </div>
</template>
<script>
    /**
     * @Description: 小险种下拉组件
     * @Props: {index: Number}  保险项在数组中索引
     * @Props: {itemdata: Object} 保险项的数据
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    export default {
        name: 'insuranceDrop',
        props: {
            index: {
                default: 0
            },
            itemdata: {}
        },
        components: {},
        data() {
            return {
                indeterminate: true,
                checkAll: false,
                checkAllGroup: [],
                insTypesDict: [],
            }
        },
        computed: {
            ...mapState('apply', [
                'orderData',
            ]),
            //根据类型返回保险项
            obj() {
                return this.itemdata
            },
            //计算文字
            checkAllGroupText() {
                let vals = this.checkAllGroup;
                let arr = [];
                vals.forEach((val) => {
                    let obj = this.insTypesDict.find(item => item.dictCode == val);
                    if (obj) arr.push(obj.dictName);
                })
                return arr.join('，');
            }
        },
        mounted() {
            this.queryDictData(['insuranceTypes']).then(dp => {
                this.insTypesDict = dp['insuranceTypes'];
            })
            this.checkAllGroup = this.itemdata.insuranceKind;
            this.checkAllGroupChange(this.checkAllGroup, true);
        },
        methods: {
            ...mapActions('dict', ['queryDictData']),
            //切换全选
            checkAllGroupChange(data, first) {
                if (data.length > 0 && data.length === this.insTypesDict.length) {
                    this.indeterminate = false;
                    this.checkAll = true;
                } else if (data.length > 0) {
                    this.indeterminate = true;
                    this.checkAll = false;
                } else {
                    this.indeterminate = false;
                    this.checkAll = false;
                }
                if (!first) this.change();
            },
            //全选险种
            handleCheckAll(i) {
                if (this.indeterminate) {
                    this.checkAll = false;
                } else {
                    this.checkAll = !this.checkAll;
                }
                this.indeterminate = false;
                if (this.checkAll) {
                    this.checkAllGroup = this.insTypesDict.map((item) => item.dictCode)
                } else {
                    this.checkAllGroup = [];
                }
                this.change()
            },
            close() {
                this.$emit('on-close')
            },
            //改变选择时，赋值给有效字段
            change() {
                this.$nextTick(() => {
                    this.$emit('on-change', {
                        i: this.index,
                        insuranceKind: this.checkAllGroup,
                        insuranceKindText: this.checkAllGroupText,
                    })
                })
            }
        },
    }
</script>



