<template>
    <div>
        <!-- 手续费信息 -->
        <div class="cm-panel" v-if="!!baseInfo && (!!baseInfo.hasFeeDiscount || isFeeProd) && !!feeDiscountInfo">
            <h3 class="margin-b-14">手续费信息</h3>
            <Form ref="feeDiscountInfo" 
                :model="feeDiscountInfo"
                :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="手续费金额" prop="poundageAmount">
                            <InputNumber disabled
                                         v-model="feeDiscountInfo.poundageAmount"
                                         placeholder="计算后带出"></InputNumber>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="手续费收取方式" prop="poundageMode">
                            <RadioGroup v-model="feeDiscountInfo.poundageMode"
                                        @on-change="handleChange(1111,'feeDiscountInfo.customDiscountType')">
                                <Radio v-for="(item) in dp['ChargeDeductMethodEnum']"
                                       disabled
                                       :label="item.dictCode" :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                </Row>
                <Row v-if="!!discountInfo && !!discountInfo.hasFeeRule">
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否贴手续费" prop="isDiscount">
                            <RadioGroup v-model="feeDiscountInfo.isDiscount"
                                        @on-change="handleChange(1111,'feeDiscountInfo.isDiscount')">
                                <Radio v-for="(item) in dp['whether']" :label="item.dictCode"
                                       :disabled="allDisabled"
                                       :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&isFeeDealerDiscount">
                        <FormItem label="经销商贴手续费金额" prop="dealerDiscountAmount"
                            :rules="ruleNum('经销商贴手续费金额', !this.isFeeDealerDiscount)">
                            <InputNumber :clearable="isFeeDealerDiscount"
                                         :disabled="allDisabled || !isFeeDealerDiscount"
                                         v-model="feeDiscountInfo.dealerDiscountAmount"
                                         @on-blur="handleChange($event,'feeDiscountInfo.dealerDiscountAmount')"
                                         @on-clear="handleChange($event,'feeDiscountInfo.dealerDiscountAmount')"
                                         :placeholder="isFeeDealerDiscount?'请输入':'不贴手续费'"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&isFeeManufacturerDiscount">
                        <FormItem label="厂商贴手续费类型" prop="manufacturerDiscountAmount">
                            <Select type="text"
                                v-model="feeType"
                                :disabled="feeDiscountMode!='3'||!isFeeManufacturerDiscount"
                                @on-change="(val)=>{handleChange(val,'feeDiscountInfo.customDiscountType', 'feeDiscountInfo')}">
                                <Option v-for="(item,index) in typeSelect" :value="item"
                                          :key="item">
                                    {{index}}
                                </Option>
                            </Select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&isFeeManufacturerDiscount">
                        <FormItem
                            ref='feeAmount' 
                            label="厂商贴手续费金额" 
                            prop="manufacturerDiscountAmount"
                            :rules="feeType === '051400000'?ruleNum('厂商贴手续费金额', this.feeDiscountMode != '3'):[]">
                            <InputNumber :clearable="feeDiscountMode=='3'"
                                         :disabled="feeDiscountMode!='3'||!isFeeManufacturerDiscount || feeType !== '051400000'"
                                         @on-blur="handleChange($event,'feeDiscountInfo.manufacturerDiscountAmount')"
                                         @on-clear="handleChange($event,'feeDiscountInfo.manufacturerDiscountAmount')"
                                         v-model="feeDiscountInfo.manufacturerDiscountAmount"
                                         :placeholder="feeType !== '051400000'?'计算后带出':feePlaceholder"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&isFeeManufacturerDiscount">
                        <FormItem
                            ref='feePer' 
                            label="厂商手续费比例(%)" 
                            prop="ratioValue"
                            :rules="feeType === '051400001'?rulePer('厂商手续费比例', this.feeDiscountMode):[]">
                            <InputNumber :clearable="rateDiscountMode=='3'"
                                         :disabled="feeDiscountMode!='3'||!isFeeManufacturerDiscount || feeType !== '051400001'"
                                         @on-blur="handleChange($event,'feeDiscountInfo.ratioValue')"
                                         @on-clear="handleChange($event,'feeDiscountInfo.ratioValue')"
                                         v-model="feeDiscountInfo.ratioValue"
                                         :placeholder="feeType !== '051400001'?'计算后带出':feePlaceholder">
                            </InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount">
                        <FormItem label="贴手续费总金额">
                            <InputNumber disabled v-model="feeDiscountInfo.discountTotalAmount"
                                         placeholder="计算后带出"></InputNumber>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
        <!-- 贴息信息 -->
        <div class="cm-panel"
             v-if="!!baseInfo && !!baseInfo.hasRateDiscount && !!discountInfo && !!discountInfo.hasRateRule && !!rateDiscountInfo">
            <h3 class="margin-b-14">贴息信息</h3>
            <Form ref="rateDiscountInfo" :model="rateDiscountInfo" :rules="rules.loanTiexiRule" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否贴息" prop="isDiscount">
                            <RadioGroup v-model="rateDiscountInfo.isDiscount"
                                        @on-change="handleChange(111,'rateDiscountInfo.isDiscount')">
                                <Radio v-for="(item) in dp['whether']" :label="item.dictCode"
                                       :disabled="allDisabled"
                                       :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateDealerDiscount">
                        <FormItem label="经销商贴息金额" prop="dealerDiscountAmount"
                                  :rules="ruleNum('经销商贴息金额', !this.isRateDealerDiscount)">
                            <InputNumber :clearable="isRateDealerDiscount"
                                         :disabled="allDisabled || !isRateDealerDiscount"
                                         v-model="rateDiscountInfo.dealerDiscountAmount"
                                         @on-blur="handleChange($event,'rateDiscountInfo.dealerDiscountAmount')"
                                         @on-clear="handleChange($event,'rateDiscountInfo.dealerDiscountAmount')"
                                         :placeholder="isRateDealerDiscount?'请输入':'不贴息'"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem label="厂商贴息类型">
                            <Select type="text"
                                @on-change="(val)=>{handleChange(val,'rateDiscountInfo.customDiscountType', 'rateDiscountInfo')}"
                                v-model="discountType"
                                :disabled="rateDiscountMode!='3' || !isRateManufacturerDiscount">
                                <Option v-for="(item,index) in typeSelect" :value="item"
                                          :key="item">
                                    {{index}}
                                </Option>
                            </Select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem
                            ref='rateAmount' 
                            label="厂商贴息金额" 
                            prop="manufacturerDiscountAmount"
                            :rules="discountType !== '051400000'?[]:ruleNum('厂商贴息金额', this.rateDiscountMode != '3')">
                            <InputNumber :clearable="rateDiscountMode=='3'"
                                         :disabled="rateDiscountMode!='3' || !isRateManufacturerDiscount || discountType !== '051400000'"
                                         @on-blur="handleChange($event,'rateDiscountInfo.manufacturerDiscountAmount')"
                                         @on-clear="handleChange($event,'rateDiscountInfo.manufacturerDiscountAmount')"
                                         v-model="rateDiscountInfo.manufacturerDiscountAmount"
                                         :placeholder="discountType !== '051400000'?'计算后带出':ratePlaceholder"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem
                            ref='ratePer' 
                            label="厂商贴息比例(%)" 
                            prop="ratioValue"
                            :rules="discountType !== '051400001'?[]:rulePer('厂商贴息比例', this.rateDiscountMode)">
                            <InputNumber :clearable="rateDiscountMode=='3'"
                                         :disabled="rateDiscountMode!='3' || !isRateManufacturerDiscount || discountType !== '051400001'"
                                         @on-blur="handleChange($event,'rateDiscountInfo.ratioValue')"
                                         @on-clear="handleChange($event,'rateDiscountInfo.ratioValue')"
                                         v-model="rateDiscountInfo.ratioValue"
                                         :placeholder="discountType !== '051400001'?'计算后带出':ratePlaceholder"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount">
                        <FormItem label="贴息总金额">
                            <InputNumber disabled v-model="rateDiscountInfo.discountTotalAmount"
                                         placeholder="计算后带出"></InputNumber>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 贴息和贴手续费
     */
    import TRANS from "./js/trans";
    import base from './js/loanbase';
    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub} from '@/utils/helper.js';
    import {amountValidator} from '@/utils/validateRules.js';

    export default {
        name: 'discountInfo',
        mixins: [base],
        data() {
            return {
                isFeeProd: false,//是否费率产品"011100001"或息费产品 "011100002"，要显示手续费的两个字段，---改成以isShowPoundage来显示了
                isFeeDealerDiscount: 0, //是否经销商贴手续费
                isFeeManufacturerDiscount: 0, //是否厂商贴手续费
                isRateDealerDiscount: 0, //是否经销商贴息
                isRateManufacturerDiscount: 0, //是否商贴息
                allDisabled: true,
                typeSelect:{
                    '金额':'051400000',
                    '比例':'051400001'
                },
                discountType:"051400000",// 厂商贴息类型
                feeType:"051400001"//手续费类型
            }
        },
        computed: {
            //是否贴手续费
            isFeeDiscount() {
                return !!this.baseInfo && !!this.feeDiscountInfo && this.feeDiscountInfo.isDiscount == 1;
            },
            //是否贴息
            isRateDiscount() {
                return !!this.baseInfo && !!this.rateDiscountInfo && this.rateDiscountInfo.isDiscount == 1;
            },
            //051400000  固定金额--1 , 051400001固定比例--2  一期没有自定义051400002--3
            feeDiscountMode() {
                if (this.feeDiscountInfo && this.feeDiscountInfo.discountMode) {
                    return this.getDiscountMethod(this.feeDiscountInfo.discountMode)
                }
                return '';
            },
            //051400000  固定金额--1 , 051400001固定比例--2  一期没有自定义051400002--3
            //厂商贴息金额：不贴息或固定金额或固定比例均置灰,自定义允许编辑
            rateDiscountMode() {
                if (this.rateDiscountInfo && this.rateDiscountInfo.discountMode) {
                    return this.getDiscountMethod(this.rateDiscountInfo.discountMode)
                }
                return '';
            },
            //厂商贴手续费金额为自定义时
            feePlaceholder() {
                var mode = this.feeDiscountMode;
                return mode == '2' ? '试算后带出' : mode == '1' ? '' : mode == '3' ? '请输入' : '';
            },
            //厂商贴息金额自定义时
            ratePlaceholder() {
                var mode = this.rateDiscountMode;
                return mode == '2' ? '试算后带出' : mode == '1' ? '' : mode == '3' ?  '请输入' : '';
            },
        },
        mounted() {
            this.queryDictData(['whether', 'ChargeDeductMethodEnum']);
            //费率产品"011100001"和息费产品 "011100002"时要显示手续费的两个字段，--改为根据isShowPoundage来判断
            this.isFeeProd = this.baseInfo.isShowPoundage == 1;
            //初始化贴息和手续费数据  未配置的话不显示
            const hasDiscount = this.isFeeProd || this.baseInfo.hasFeeDiscount == 1 || this.baseInfo.hasRateDiscount == 1;
            if (hasDiscount && this.financePeriodYear > 0) {
                this.reloadDiscountInfo();
            } else this.allDisabled = true;
        },
        methods: {
            //返回贴息方式的种类：051400000  固定金额--1 , 051400001固定比例--2  一期没有自定义051400002--3
            getDiscountMethod(mode) {
                const obj = {
                    "051400000": "1",
                    "051400001": "2",
                    "051400002": "3",
                }
                return obj[mode];
            },
            //初始请求和刷新请求贴息数据
            //贷款基本信息中融资期限修改且选择了贴息时需要重新请求贴息数据
            reloadDiscountInfo(reloadFlag) {
                this.showLoading();
                let url = polarisserver + '/finance/query/financeDiscountInfo';
                let baseInfo = this.baseInfo;
                let orderinfo = this.orderData.orderinfo;
                //请求贴息方式需要带上参数
                let params = {
                    "orderNo": this.$route.params.orderNo,
                    "productCode": orderinfo.productCode,
                    "version": orderinfo.productVersion,
                    "term": baseInfo.financePeriod,
                    "masterBrandId": orderinfo.carMasterBrandId,
                    "brandId": orderinfo.carBrandId,
                    "styleId": orderinfo.carStyleId,
                    "modelId": orderinfo.carModelId,
                    "isShowPoundage": this.baseInfo.isShowPoundage,
                }
                this.$http.post(url, params).then(res => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success || !result.data) return this.errorModal(result.errorMessage || '贴息和贴手续费请求失败');
                    let data = result.data;
                    this.discountInfo = TRANS.transToForm('discountInfo', data);
                    this.feeDiscountInfo = this.discountInfo.feeDiscountInfo;
                    this.rateDiscountInfo = this.discountInfo.rateDiscountInfo;
                    if(this.rateDiscountInfo){
                        this.discountType = this.rateDiscountInfo.discountMode !== "051400002"?this.rateDiscountInfo.discountMode:this.rateDiscountInfo.customDiscountType||"051400000"
                    }
                    if(this.feeDiscountInfo){
                        this.feeType = this.feeDiscountInfo.discountMode !== "051400002"?this.feeDiscountInfo.discountMode:this.feeDiscountInfo.customDiscountType||"051400000"
                    }
                    if (!!this.feeDiscountInfo) {
                        //经销商是否贴手续费
                        this.isFeeDealerDiscount = this.feeDiscountInfo.isDealerDiscount == 1;
                        //厂商是否贴手续费
                        this.isFeeManufacturerDiscount = this.feeDiscountInfo.isManufacturerDiscount == 1;
                    }
                    if (!!this.rateDiscountInfo) {
                        //经销商是否贴息isRateDealerDiscount，如果否的话禁用经销商贴息金额，如果是的话可以贴也可以不贴，就是可编辑，但不校验必填
                        this.isRateDealerDiscount = this.rateDiscountInfo.isDealerDiscount == 1;
                        //是否厂商贴息
                        this.isRateManufacturerDiscount = this.rateDiscountInfo.isManufacturerDiscount == 1;
                    }
                    this.setLoanData({
                        name: 'discountInfo',
                        data: this.discountInfo
                    })
                    this.allDisabled = false;
                    this.$nextTick(() => {
                        if (reloadFlag) this.cacheData('discountInfo');//如果重新请求的话，触发缓存
                        this.todoRecount();
                    })
                }).catch(err => {
                    this.hideLoading();
                    this.errorCatchModal('贴息和贴手续费请求出错：', err)
                    this.allDisabled = true;
                })
            },

            ruleNum(text,mode){
                if(mode) return []
                return [amountValidator(text, false)]
            },
            //生成自定义厂商贴手续比例校验
            rulePer(text, mode) {
                if (mode != '3') return [];
                return [amountValidator(text, false, 100, 0)]
            },

            // input改变值
            handleChange(val, handleField, type) {
                if(type){
                    let keys = handleField.split('.')
                    this[keys[0]][keys[1]] = val

                    if(type === 'feeDiscountInfo'){
                        this.$refs.feeAmount.resetField()
                        this.$refs.feePer.resetField()
                    }else{
                        this.$refs.rateAmount.resetField()
                        this.$refs.ratePer.resetField()
                    }
                    this[type].manufacturerDiscountAmount = null
                    this[type].ratioValue = null
                }
                //判断值是否改变
                if (!this.checkFieldIsChange('discountInfo.' + handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('discountInfo');
                    this.todoRecount();
                })
            },

            //校验当前表单，可由父页面调用
            validate(cb) {
                let refName = ['feeDiscountInfo', 'rateDiscountInfo']
                let arr = [];
                refName.forEach(ref => {
                    if (this.$refs[ref])
                        arr.push(this.$refs[ref].validate())
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },
        },
    }
</script>



