<template>
    <div>
        <div class="cm-panel" v-if="!!insuranceInfo">
            <h3 class="margin-b-14">保险信息</h3>
            <Form ref="insuranceInfo" :model="insuranceInfo" :rules="rules.loanInsuranceRule" :label-width="120"
                  v-if="insuranceInfo">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否融保险" prop="isFinanceInsurance">
                            <RadioGroup v-model="insuranceInfo.isFinanceInsurance"
                                        v="insuranceInfo.isFinanceInsurance"
                                        @on-change="val=>{handleChange(val,'insuranceInfo.isFinanceInsurance')}">
                                <Radio v-for="(item,index) in DICT_whetherIns"
                                       :disabled="insuranceInfo.isRequire=='1'"
                                       :label="item.dictCode" :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isBuyInsurance">
                        <FormItem label="保险出单地" prop="insuranceChannel">
                            <i-select type="text" v-model="insuranceInfo.insuranceChannel"
                                      v="insuranceInfo.insuranceChannel"
                                      @on-change="val=>{handleChange(val,'insuranceInfo.insuranceChannel')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['billingPlace']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isBuyInsurance">
                        <FormItem label="商业险出单方式" prop="insuranceMode">
                            <i-select type="text" v-model="insuranceInfo.insuranceMode"
                                      v="insuranceInfo.insuranceMode"
                                      @on-change="val=>{handleChange(val,'insuranceInfo.insuranceMode')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in dp['insuranceMode']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>

                </Row>
                <!--保险年份表格-->
                <div class="ivu-table-wrapper input-edit-table table-only-horizon-line" v-if="isBuyInsurance">
                    <div class="ivu-table">
                        <div class="ivu-table-body">
                            <table cellspacing="0" cellpadding="0" border="0" style="width: 100%;">
                                <tbody class="ivu-table-tbody">
                                <tr>
                                    <th width="180">
                                        <div class="ivu-table-cell"><span>融资项</span></div>
                                    </th>
                                    <th>
                                        <div class="ivu-table-cell"><span>保险类型</span></div>
                                    </th>
                                    <th width="180">
                                        <div class="ivu-table-cell"><span>融资额(元)</span></div>
                                    </th>
                                </tr>
                                <tr class="ivu-table-row" v-for="(obj,i) in insuranceInfo.insuranceItemList" :key="i"
                                    v-if="parseInt(obj.period)<=financePeriodYear || obj.financeCode.length>0">
                                    <td width="180">
                                        <div class="ivu-table-cell"><span>{{yearName(i)}}保险</span></div>
                                    </td>
                                    <td>
                                        <div class="ivu-table-cell">
                                            <Row :gutter="10" class="insurance-drop-row">
                                                <i-col span="12">
                                                    <Form-item :prop="'insuranceItemList.' + i + '.financeCode'"
                                                               :rules="createClassRule(i)">
                                                        <div style="position: relative"
                                                             v-clickoutside:click="insuranceClassClickOutFn">
                                                            <Poptip trigger="hover" placement="top-start">
                                                                <i-select multiple style="display:none"
                                                                          v-model="obj.financeCode"
                                                                          class="ivu-select-visible"></i-select>
                                                                <i-input type="text" readonly size="small"
                                                                         :value="getInsuranceClassText(obj.financeCode,i)"
                                                                         placeholder="请选择"
                                                                         @click.native.stop="insuranceClassClickFn(i)">
                                                                </i-input>
                                                                <i class="ivu-icon ivu-icon-ios-arrow-down ivu-select-arrow"
                                                                   style="position:absolute;z-index:9"
                                                                   :style="showClassDrop===i?{transform: 'rotate(180deg)'}:{}"
                                                                   @click.stop="insuranceClassClickFn(i)"></i>
                                                                <div slot="content" class="insurance-drop-tip">
                                                                    {{getInsuranceClassText(obj.financeCode,i) ||
                                                                    '未选择'}}
                                                                </div>
                                                            </Poptip>
                                                            <insurance-class-drop :index="i"
                                                                                  :itemdata="obj"
                                                                                  @on-change="insuranceClassDropChange"
                                                                                  @on-close="insuranceClassClickOutFn"
                                                                                  v-show="showClassDrop===i"
                                                                                  @click.native.stop="()=>{}"></insurance-class-drop>
                                                        </div>
                                                    </Form-item>
                                                </i-col>
                                                <i-col span="12" v-if="isHasBusiIns(obj,i)">
                                                    <Form-item :prop="'insuranceItemList.' + i + '.insuranceKind'"
                                                               :rules="createKindRule(i)">
                                                        <div style="position: relative"
                                                             v-clickoutside:click="insuranceClickOutFn">
                                                            <Poptip trigger="hover" placement="top-start">
                                                                <i-select multiple style="display:none"
                                                                          v-model="obj.insuranceKind"></i-select>
                                                                <i-input type="text" readonly size="small"
                                                                         :value="getInsuranceKindText(obj.insuranceKind)"
                                                                         placeholder="请选择"
                                                                         @click.native.stop="insuranceClickFn(i)">
                                                                </i-input>
                                                                <i class="ivu-icon ivu-icon-ios-arrow-down ivu-select-arrow"
                                                                   style="position:absolute;z-index:9"
                                                                   :style="showDrop===i?{transform: 'rotate(180deg)'}:{}"
                                                                   @click.stop="insuranceClickFn(i)"></i>
                                                                <div slot="content" class="insurance-drop-tip">
                                                                    {{getInsuranceKindText(obj.insuranceKind) || '未选择'}}
                                                                </div>
                                                            </Poptip>
                                                            <insurance-drop :index="i"
                                                                            :itemdata="obj"
                                                                            @on-change="insuranceDropChange"
                                                                            @on-close="insuranceClickOutFn"
                                                                            v-show="showDrop===i"
                                                                            @click.native.stop="()=>{}"></insurance-drop>
                                                        </div>
                                                    </Form-item>
                                                </i-col>
                                            </Row>
                                        </div>
                                    </td>
                                    <td width="180">
                                        <div class="ivu-table-cell">
                                            <Form-item :prop="'insuranceItemList.' + i + '.insuranceAmount'"
                                                       v-if="obj.financeCode.length>0"
                                                       v-show="obj.insuranceType != ''" :rules="createAmountRule(i)">
                                                <InputNumber
                                                        v-model="obj.insuranceAmount" size="small"
                                                        :clearable="obj.financeCode.length>0 && isHasBusiIns(obj,i)"
                                                        :disabled="obj.financeCode.length>0 && !isHasBusiIns(obj,i)"
                                                        :v="'insuranceInfo.insuranceItemList.' + i + '.insuranceAmount'"
                                                        @on-blur="handleBlur"
                                                        @on-clear="handleBlur"
                                                        placeholder="请输入"></InputNumber>
                                            </Form-item>
                                        </div>
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Form>
        </div>
        <div class="cm-panel" v-if="!!gpsInfo">
            <h3 class="margin-b-14">GPS信息</h3>
            <Form ref="gpsInfo" :model="gpsInfo" :rules="rules.loanGpsRule" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS厂商" prop="gpsSupplier">
                            <i-select type="text" v-model="gpsInfo.gpsSupplier"
                                      v="gpsInfo.gpsSupplier"
                                      @on-change="val=>{handleChange(val,'gpsInfo.gpsSupplier')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in gpsFactoryDictList" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <!--输入型-->
                        <Form-item label="GPS基础价金额" prop="basePrice" v-if="isInputPrice(gpsInfo.basePriceType)"
                                   :rules="createGpsBasePriceRule()">
                            <InputNumber clearable
                                         v-model="gpsInfo.basePrice"
                                         v="gpsInfo.basePrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="createItemPlaceholder(gpsInfo.basePriceMin, gpsInfo.basePriceMax)"></InputNumber>
                            <!--:max="obj.maxAmount" :min="obj.minAmount"-->
                        </Form-item>
                        <!--下拉型-->
                        <Form-item label="GPS基础价金额" prop="basePrice" v-if="isPickerPrice(gpsInfo.basePriceType)"
                                   :rules="createGpsBasePriceRule()">
                            <!--{required: true, message: obj.financeName +'不能为空', trigger: 'change'}-->
                            <!--:disabled="gpsInfo.basePriceGear&&gpsInfo.basePriceGear.length==1"-->
                            <i-select type="text"
                                      v-model="gpsInfo.basePrice"
                                      v="gpsInfo.basePrice"
                                      @on-change="val=>{handleChange(val,'gpsInfo.basePrice')}"
                                      placeholder="请选择">
                                <i-option
                                        v-for="(item,index) in (gpsInfo.basePriceGear || []).map(gear=>{return {name:formatAmount(gear.value),value:gear.value}})"
                                        :value="item.value"
                                        :key="index">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                        <!--固定比例   gps不会有固定比例-->
                        <!--<Form-item label="GPS基础价金额" prop="basePrice"-->
                        <!--v-if="isRatioPrice(gpsInfo.basePriceType)"-->
                        <!--:rules="createGpsBasePriceRule()">-->
                        <!--<Input disabled v-model="gpsInfo.basePrice"-->
                        <!--placeholder="固定比例"></Input>-->
                        <!--</Form-item>-->
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <!--输入型-->
                        <Form-item label="GPS加价金额" prop="increasePrice" v-if="isInputPrice(gpsInfo.increasePriceType)"
                                   :rules="createGpsIncreasePriceRule()">
                            <InputNumber clearable
                                         v-model="gpsInfo.increasePrice"
                                         v="gpsInfo.increasePrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="createItemPlaceholder(gpsInfo.increasePriceMin, gpsInfo.increasePriceMax)"></InputNumber>
                        </Form-item>
                        <!--下拉型-->
                        <Form-item label="GPS加价金额" prop="increasePrice" v-if="isPickerPrice(gpsInfo.increasePriceType)"
                                   :rules="createGpsIncreasePriceRule()">
                            <!--{required: true, message: obj.financeName +'不能为空', trigger: 'change'}-->
                            <!--:disabled="gpsInfo.increasePriceGear&&gpsInfo.increasePriceGear.length==1"-->
                            <i-select type="text"
                                      v-model="gpsInfo.increasePrice"
                                      v="gpsInfo.increasePrice"
                                      @on-change="val=>{handleChange(val,'gpsInfo.increasePrice')}"
                                      placeholder="请选择">
                                <i-option
                                        v-for="(item,index) in (gpsInfo.increasePriceGear || []).map(gear=>{return {name:formatAmount(gear.value),value:gear.value}})"
                                        :value="item.value"
                                        :key="index">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                        <!--固定比例  gps不会有固定比例-->
                        <!--<Form-item label="GPS加价金额" prop="increasePrice" v-if="isRatioPrice(gpsInfo.increasePriceType)"-->
                        <!--:rules="createGpsBasePriceRule()">-->
                        <!--<Input disabled v-model="gpsInfo.increasePrice" placeholder="固定比例"></Input>-->
                        <!--</Form-item>-->
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS加价放款类型" prop="increaseFinanceType">
                            <i-select type="text" v-model="gpsInfo.increaseFinanceType"
                                      v="gpsInfo.increaseFinanceType"
                                      @on-change="val=>{handleChange(val,'gpsInfo.increaseFinanceType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in DICT_gpsIncreaseFinanceType" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
        <div class="cm-panel" v-if="!!theftInsuranceInfo">
            <h3 class="margin-b-14">盗抢责任险信息</h3>
            <Form ref="theftInsuranceInfo" :model="theftInsuranceInfo" :rules="rules.loanTheftRule||{}"
                  :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="是否有盗抢险" prop="isFinanceInsurance">
                            <RadioGroup v-model="theftInsuranceInfo.isFinanceInsurance"
                                        v="theftInsuranceInfo.isFinanceInsurance"
                                        @on-change="val=>{handleChange(val,'theftInsuranceInfo.isFinanceInsurance')}">
                                <Radio v-for="(item,index) in DICT_whetherTheft" :label="item.dictCode"
                                       :disabled="isTheftItemDisabled && item.dictCode=='1'"
                                       :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6"  v-if="isBuyTheftInsurance && isOnline">
                        <Form-item label="是否赠送" prop="isGift">
                            <RadioGroup v-model="theftInsuranceInfo.isGift"
                                        v="theftInsuranceInfo.isGift"
                                        @on-change="val=>{handleChange(val,'theftInsuranceInfo.isGift')}">
                                <Radio v-for="item in DICT_isGif" :label="item.dictCode" :key="item.dictCode">
                                    <span>{{item.dictName}}</span>
                                </Radio>
                            </RadioGroup>
                        </Form-item>
                    </i-col>
                </Row>
                <Row v-if="isGift">
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="盗抢责任险加融期限" prop="increaseFinancePeriod">
                            <i-select type="text" v-model="theftInsuranceInfo.increaseFinancePeriod"
                                      v="theftInsuranceInfo.increaseFinancePeriod"
                                      @on-change="val=>{handleChange(val,'theftInsuranceInfo.increaseFinancePeriod')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in theftPeriodDict" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="盗抢责任险加价类型" prop="increasePriceType">
                            <i-select type="text" v-model="theftInsuranceInfo.increasePriceType"
                                      :disabled="!theftInsuranceInfo.increaseFinancePeriod"
                                      v="theftInsuranceInfo.increasePriceType"
                                      @on-change="val=>{handleChange(val,'theftInsuranceInfo.increasePriceType')}"
                                      placeholder="请选择">
                                <i-option v-for="(item,index) in DICT_theftIncreaseType" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="盗抢责任险基础价" prop="basePrice">
                            <InputNumber disabled
                                         v-model="theftInsuranceInfo.basePrice"
                                         v="theftInsuranceInfo.basePrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="盗抢责任险加价" prop="increasePrice" :rules="createTheftIncreasePriceRule">
                            <InputNumber :clearable="!!theftInsuranceInfo.increaseFinancePeriod"
                                         :disabled="!theftInsuranceInfo.increaseFinancePeriod"
                                         v-model="theftInsuranceInfo.increasePrice"
                                         v="theftInsuranceInfo.increasePrice"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         placeholder="请输入"></InputNumber>
                            <!--:placeholder="theftInsuranceInfo.increaseMinPrice||0 + '~'+theftInsuranceInfo.increaseMaxPrice"></Input>-->
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
        <div class="cm-panel" v-if="otherFinanceInfo&&otherFinanceInfo.length">
            <h3 class="margin-b-14">其他融资项</h3>
            <Form ref="otherFinanceInfo" :model="itemInfo" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-for="(obj,i) in itemInfo.otherFinanceInfo" :key="i"
                           v-if="!isDisabledItem(obj) && !(isGpsIncreaseBasePriceCode(obj.financeCode)&& isGpsIncreaseItemDisabled) && !(isGpsIncreaseIncreasePriceCode(obj.financeCode)&& isGpsIncreaseItemDisabled)">
                        <!--输入型-->
                        <Form-item :label="toUpper(obj.financeName)" :prop="'otherFinanceInfo.' + i + '.financePrice'"
                                   v-if="isInputPrice(obj.financeType)" :rules="createRule(obj)">
                            <InputNumber clearable
                                         v-model="obj.financePrice"
                                         :v="'otherFinanceInfo.' + i + '.financePrice'"
                                         :disabled="gpsIncreaseIncreasePriceDisabled&&isGpsIncreaseIncreasePriceCode(obj.financeCode)"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="createItemPlaceholder(obj.financePriceMin, obj.financePriceMax)"></InputNumber>
                        </Form-item>
                        <!--下拉型-->
                        <Form-item :label="toUpper(obj.financeName)" :prop="'otherFinanceInfo.' + i + '.financePrice'"
                                   v-if="isPickerPrice(obj.financeType)" :rules="createRule(obj)">
                            <!--:disabled="obj.financeGear.length == 1"-->
                            <i-select
                                    v-model="obj.financePrice"
                                    :v="'otherFinanceInfo.' + i + '.financePrice'"
                                    :disabled="gpsIncreaseIncreasePriceDisabled&&isGpsIncreaseIncreasePriceCode(obj.financeCode)"
                                    @on-change="val=>{handleChange(val,'otherFinanceInfo.' + i + '.financePrice')}"
                                    placeholder="请选择">
                                <i-option
                                        v-for="(item,index) in (obj.financeGear || []).map(gear=>{return {name:formatAmount(gear.value),value:gear.value}})"
                                        :value="item.value"
                                        :key="item.value">
                                    {{item.name}}
                                </i-option>
                            </i-select>
                        </Form-item>
                        <!--固定比例-->
                        <Form-item :label="toUpper(obj.financeName)" :prop="'otherFinanceInfo.' + i + '.financePrice'"
                                   v-if="isRatioPrice(obj.financeType)">
                            <InputNumber disabled
                                         v-model="obj.financePrice"
                                         :placeholder="obj.financePrice===''||obj.financePrice==='null'?'计算后带出':''"></InputNumber>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 保险/GPS/盗抢险/其他融资项
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import base from './js/loanbase';
    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub, transDateToString, stringToNumber} from '@/utils/helper.js';
    import {amountValidator} from '@/utils/validateRules';
    import vFilter from '@/utils/filters';
    //险别三年对应融资项编码，不是字典码
    const classArr = yx.classArr;

    export default {
        name: 'itemInfo',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                visible: false,
                inited: false,//页面初始渲染并切换完成
                showDrop: undefined,
                showClassDrop: undefined,
                DICT_whetherIns: [],//是否融保险 字典
                DICT_whetherTheft: [],//是否融盗抢险 字典
                DICT_theftIncreaseType: [],//盗抢险加价类型 字典
                DICT_gpsIncreaseFinanceType: [],//GPS加价类放款类型 字典
                theftTotalLimit: "",//盗抢险加价总价限额
                isInputPrice: yx.isInputPrice,
                isPickerPrice: yx.isPickerPrice,
                isRatioPrice: yx.isRatioPrice,
                formatAmount: vFilter.number,
                gpsIncreaseIncreasePriceDisabled: false,//gps升级方案加价禁用（在gps升级方案基础价为零时）
                DICT_isGif:[ //是否赠送盗抢险 字典
                    {
                        dictCode:'0',
                        dictName:'否'
                    },{
                        dictCode:'1',
                        dictName:'是'
                    }
                ],
            }
        },
        watch:{
            isOnline:{
                handler(newValue, oldValue){
                    if(!newValue){
                        this.theftInsuranceInfo.isGift = ''
                    }
                }
            }
        },
        computed: {
            //一级渠道为线上时盗抢险不可融
            isTheftItemDisabled() {
                let res = yx.isTheftItemDisabled(this.orderData.loan)
                if (res && !!this.theftInsuranceInfo) this.theftInsuranceInfo.isFinanceInsurance = '0';
                return res;
            },
            //一级渠道为线上时gps升级方案不显示；为线下且融盗抢险时gps升级方案不显示
            isGpsIncreaseItemDisabled() {
                let res = yx.isGpsIncreaseItemDisabled(this.orderData.loan)
                return res;
            },
            
            // 一级渠道为线上
            isOnline(){
                return this.orderData.loan.baseInfo.custPrimaryChannel === '030100000'? true: false
            },
            
            //生成保险项金额输入框的校验
            createAmountRule() {
                return (i) => {
                    let obj = this.insuranceInfo.insuranceItemList[i];
                    let needed = obj.isRequireFinanceCodeArr;
                    let rule = [];
                    //选择总部时有保险金额的下限
                    let isZongbu = this.insuranceInfo.insuranceChannel == '2';
                    let min = (!yx.isEmpty(obj['insurancMinPrice'])) ? parseFloat(obj['insurancMinPrice'] || 0) : 0;
                    let max = !yx.isEmpty(obj['insuranceMaxPrice']) ? parseFloat(obj['insuranceMaxPrice'] || 0) : '';
                    let hasBusi = this.isHasBusiIns(obj);//有商业险
                    let isFirstYear = this.insuranceInfo.isFinanceInsurance == '1' && i == 0;//第一年时商业险必融
                    let validator = (rule, value, callback) => {
                        let filed = this.yearName(i) + '保险金额';
                        if (yx.isEmpty(value)) {
                            if (hasBusi || isFirstYear)
                                callback(new Error(filed + '不能为空'));
                            else
                                callback();
                        } else if (!hasBusi) {//交强险有值且没有商业险时不进行校验
                            callback();
                        } else if (!(yx.amountReg.test(value))) {
                            callback(new Error(filed + '填写错误'));
                        } else if ((parseFloat(value) <= 0)) {
                            callback(new Error(filed + '必须大于0'));
                        } else if (!yx.isEmpty(min) && parseFloat(value) < min) {
                            callback(new Error(filed + '只能输入' + min + '-' + max));
                        } else if (!yx.isEmpty(max) && parseFloat(value) > max) {
                            callback(new Error(filed + '只能输入' + min + '-' + max));
                        } else if (String(value).length > 12) {
                            callback(new Error(filed + '不能超过12位'))
                        } else {
                            callback();
                        }
                    };
                    rule.push({
                        validator: validator,
                        trigger: 'change'
                    })
                    return rule;
                }
            },

            //生成盗抢责任险加价金额的校验  此字段为整数，其他金额都可以小数  只要选择融就必填
            createTheftIncreasePriceRule() {
                let obj = {
                    financeName: "盗抢责任险加价",
                    isRequire: false,//没有必融的概念，加价可以为0
                    financeType: this.theftInsuranceInfo.increasePriceType,
                    financePriceMax: this.calcTheftTotalLimit() - this.calcTheftBasePrice(),
                    financePriceMin: 0,
                }
                let rule = [];
                let isNeeded = obj['isRequire'] == '1';
                let min = !yx.isEmpty(obj['financePriceMin']) ? parseFloat(obj['financePriceMin'] || 0) : 0;
                let max = !yx.isEmpty(obj['financePriceMax']) ? parseFloat(obj['financePriceMax'] || 0) : '';
                let filed = '盗抢责任险加价';
                rule.push(amountValidator(filed, isNeeded, max, min));
                return rule;
            },
            // 是否赠送盗抢险
            isGift(){
                // 线上且赠送
                // 线下且有盗抢险
                return this.isBuyTheftInsurance && ((this.isOnline &&this.theftInsuranceInfo.isGift == '0')||(!this.isOnline && this.isBuyTheftInsurance))? true: false
            }
        },
        mounted() {
            this.queryDictData(['whether', 'theftIncreasedType', 'billingPlace', 'insuranceMode', 'insuranceTypes']).then(dp => {
                this.DICT_whetherIns = dp['whether'];
                this.DICT_whetherTheft = dp['whether'];
                this.DICT_theftIncreaseType = dp['theftIncreasedType'];
                this.DICT_gpsIncreaseFinanceType = dp['theftIncreasedType'];

                this.queryInitData('itemInfo', () => {
                    //GPS加价放款类型字典根据一级代理商判断: 客户一级科目为代理商时不能选择对私
                    if (!!this.gpsInfo && this.orderData.orderinfo.channelOneLevel == '04970000') {
                        this.DICT_gpsIncreaseFinanceType = this.dp['theftIncreasedType'].filter(item => item.dictCode != '1');
                    }
                    //盗抢险加价类型字典根据一级代理商判断: 客户一级科目为代理商时不能选择对私
                    if (!!this.theftInsuranceInfo && this.orderData.orderinfo.channelOneLevel == '04970000') {
                        this.DICT_theftIncreaseType = this.dp['theftIncreasedType'].filter(item => item.dictCode != '1');
                    }
                    //初始更新盗抢险基础价
                    if (!!this.theftInsuranceInfo) {
                        this.updateTheftBasePrice();
                    }
                    //gps升级方案基础价为0时对应gps升级方案加价逻辑处理
                    if (!!this.otherFinanceInfo) {
                        var index = this.otherFinanceInfo.findIndex(item => this.isGpsIncreaseBasePriceCode(item.financeCode));
                        if (index > -1) this.handleGpsIncreaseBasePriceChange(index);
                    }

                    //统计一次
                    this.$nextTick(() => {
                        this.todoRecount();
                        this.inited = true;
                    })
                })
            })
        },
        methods: {
            //有最大值和最小值时显示placeholder
            createItemPlaceholder(min, max) {
                if (min && max)
                    return min + '~' + max;
                else return '请输入';
            },
            //融资项为车款时不显示在页面,不校验,
            isDisabledItem(obj) {
                let code = obj.financeCode;
                return yx.isCarItemCode(code)
            },
            //是GPS升级方案基础价
            isGpsIncreaseBasePriceCode(code) {
                return yx.isGpsIncreaseBasePriceCode(code)
            },
            //是GPS升级方案加价
            isGpsIncreaseIncreasePriceCode(code) {
                return yx.isGpsIncreaseIncreasePriceCode(code)
            },
            //转换大写
            toUpper(str) {
                return String(str).toUpperCase();
            },

            //切换保险基本信息中某些字段时清空其它值 ，不改变原对象
            todoInsuranceInfoSwitch() {
                let obj = this.insuranceInfo;
                let financeYear = this.financePeriodYear;
                //必融判断，这是根据年份中的必融项生成的，只要有一个必融就是1，所以不用外面的判断
                //未选择融保险时清空数据
                if (obj.isFinanceInsurance != '1') {
                    obj.insuranceChannel = "";
                    obj.insuranceMode = "";
                }
                _.each(obj.insuranceItemList, (item) => {
                    let hasBusiIns = this.isHasBusiIns(item);
                    //未选择融保险，或者这一年大于融资年且没有必融项时清空数据
                    if (obj.isFinanceInsurance != '1' || (_.isEmpty(item.isRequireFinanceCode) && parseInt(item.period || 0) > financeYear)) {
                        item.financeCode = typeof item.financeCode == 'string' ? item.financeCode.split(',') : (item.financeCode || []);
                        item.insuranceKind = typeof item.insuranceKind == 'string' ? item.insuranceKind.split(',') : (item.insuranceKind || []);
                        item.insuranceAmount = 0;
                    } else {//融保险且有效年份时，只有交强车船时带入基本信息中的固定金额，否则保留原金额
                        if (item.financeCode.length == 1 && !hasBusiIns) {
                            item.insuranceKind.splice(0);
                            item.insuranceAmount = stringToNumber(this.baseInfo.carVesselCompulsoryInsurancePrice);
                        } else if (!hasBusiIns) {
                            item.insuranceKind.splice(0);
                        }
                    }
                })
                return obj;
            },

            //生成险别校验条件
            createClassRule(i) {
                let obj = this.insuranceInfo.insuranceItemList[i];
                let rule = [];
                let isFirstYear = this.insuranceInfo.isFinanceInsurance == '1' && i == 0;//第一年时商业险必融
                let needed = obj.isRequireFinanceCodeArr;
                rule.push({
                    validator: (rule, value, callback) => {
                        let filed = this.yearName(i);
                        if (needed.length > value.length) {
                            let arr = [];
                            needed.forEach(item => {
                                if (value.indexOf(item) < 0)
                                    arr.push(item)
                            })
                            let textsArr = [];
                            classArr[i].forEach(item => {
                                if (arr.indexOf(item.dictCode) > -1)
                                    textsArr.push(item.dictName)
                            })
                            let texts = textsArr.join(',')
                            callback(new Error(filed + texts + '必融'))
                        } else if (isFirstYear && !value.length) {
                            callback(new Error(filed + '保险必融'))
                        } else {
                            callback();
                        }
                    },
                    trigger: 'change'
                })
                return rule;
            },
            //生成险种校验条件
            createKindRule(i) {
                let obj = this.insuranceInfo.insuranceItemList[i];
                let rule = [];
                if (this.isHasBusiIns(obj, i)) {//下拉且必填时校验
                    rule.push({
                        type: 'array',
                        validator: (rule, value, callback) => {
                            let filed = this.yearName(i) + '';
                            if (value.length == 0) {
                                callback(new Error(filed + '险种不能为空'))
                            } else {
                                callback();
                            }
                        },
                        trigger: 'change'
                    })
                }
                return rule;
            },
            //保险年份项中是否选择了商业险，每年商业险编码不一样
            isHasBusiIns(obj) {
                let busiCode = classArr[parseInt(obj.period) - 1][0].dictCode;
                return obj.financeCode.indexOf(busiCode) > -1;
            },
            //获取险别描述
            getInsuranceClassText(vals, i) {
                let arr = [];
                let obj = this.insuranceInfo.insuranceItemList[i];
                vals.forEach((val) => {
                    let row = classArr[parseInt(obj.period) - 1].find(item => item.dictCode == val);
                    if (row) arr.push(row.dictName);
                })
                arr.sort((a, b) => {
                    return a > b ? 1 : -1;
                })
                return arr.join('，');
            },
            //获取险种描述
            getInsuranceKindText(vals) {
                let arr = [];
                vals.forEach((val) => {
                    let obj = this.dp['insuranceTypes'].find(item => item.dictCode == val);
                    if (obj) arr.push(obj.dictName);
                })
                return arr.join('，');
            },
            //获取第几年
            yearName(i) {
                let obj = this.insuranceInfo.insuranceItemList[i];
                i = parseInt(obj.period) - 1;//年份也是
                return '第' + ["一", "二", "三", "四", "五"][i] + '年';
            },

            //点击险别输入框时切换下拉
            insuranceClassClickFn(i) {
                if (this.showClassDrop === i) this.showClassDrop = '';
                else this.showClassDrop = i;
                this.showDrop = '';
            },
            //险别输入框外点击时隐藏下拉
            insuranceClassClickOutFn(i) {
                this.showClassDrop = '';
            },
            //接收传回来的险别值，包括id和text数组，保存到store
            insuranceClassDropChange(obj) {
                let row = this.insuranceInfo.insuranceItemList[obj.i];
                row.financeCode = obj.financeCode;
                row.financeCodeText = obj.financeCodeText;
                if (!this.isHasBusiIns(row)) {
                    row.insuranceAmount = '0';
                }
                this.$nextTick(() => {
                    if (this.inited) this.handleChange(row.financeCode, 'insuranceInfo.insuranceItemList.' + obj.i + '.financeCode')
                })
            },
            //点击险种输入框时切换下拉
            insuranceClickFn(i) {
                if (this.showDrop === i) this.showDrop = '';
                else this.showDrop = i;
                this.showClassDrop = '';
            },
            //险种输入框外点击时隐藏下拉
            insuranceClickOutFn(i) {
                this.showDrop = '';
            },
            //接收传回来的险种值，包括id和text数组，保存到store
            insuranceDropChange(obj) {
                let row = this.insuranceInfo.insuranceItemList[obj.i];
                row.insuranceKind = obj.insuranceKind;
                row.insuranceKindText = obj.insuranceKindText;
                this.$nextTick(() => {
                    if (this.inited) this.handleChange(row.insuranceKind, 'insuranceInfo.insuranceItemList.' + obj.i + '.insuranceKind')
                })
            },
            //生成gps基础价的校验  整数位吗？
            createGpsBasePriceRule() {
                let obj = {
                    financeName: "GPS基础价",
                    isRequire: this.gpsInfo.isRequireBasePrice,
                    financeType: this.gpsInfo.basePriceType,
                    financePriceMax: this.gpsInfo.basePriceMax,
                    financePriceMin: this.gpsInfo.basePriceMin,
                }
                return this.createRule(obj)
            },
            //生成gps加价金额的校验   整数位
            createGpsIncreasePriceRule() {
                let obj = {
                    financeName: "GPS加价金额",
                    isRequire: this.gpsInfo.isRequireIncreasePrice,
                    financeType: this.gpsInfo.increasePriceType,
                    financePriceMax: this.gpsInfo.increasePriceMax,
                    financePriceMin: this.gpsInfo.increasePriceMin,
                }
                return this.createRule(obj)
            },
            //计算盗抢险基础价和盗抢险加价限额
            updateTheftBasePrice() {
                this.theftInsuranceInfo.basePrice = '' + this.calcTheftBasePrice();
                this.theftTotalLimit = '' + this.calcTheftTotalLimit();
            },
            //计算盗抢险基础价 结果是个整数
            //总价限额   基础150
            // 车辆价值小于30万的保费：每增加一年，保费增加150元；车价每增加30万，保费增加300；
            calcTheftBasePrice() {
                var period = this.theftInsuranceInfo.increaseFinancePeriod || 0;
                var baseamount = this.sellingAmount || 0;
                if (period == 0 || baseamount == 0) {
                    return 0;
                } else {
                    //基础价
                    var yearbase = 150 + (Math.ceil(baseamount / 300000) - 1) * 300;
                    var basePrice = yearbase + (parseInt(period) - 1) * 150;
                    return basePrice;
                }
            },
            //计算盗抢险加价限额 结果是个整数
            //总价限额   基础500
            // 车辆价值小于30万的保费：每增加一年，保费增加500元；车价每增加30万，保费增加300；
            calcTheftTotalLimit() {
                var period = this.theftInsuranceInfo.increaseFinancePeriod || 0;
                var baseamount = this.sellingAmount || 0;
                if (period == 0) {
                    return 0;
                } else {
                    var yearincreasebase = 500 + (Math.ceil(baseamount / 300000) - 1) * 300;
                    var increasPrice = yearincreasebase + (parseInt(period) - 1) * 500;
                    return increasPrice;
                }
            },
            //处理gps升级方案基础价为0时，gps升级方案加价为0且禁用，如果档位中没有0则不动
            handleGpsIncreaseBasePriceChange(index) {
                var obj = this.otherFinanceInfo[index];
                //是gps升级方案基础价
                if (obj && this.isGpsIncreaseBasePriceCode(obj.financeCode)) {
                    let val = obj.financePrice;
                    //找到gps升级方案加价选项  在gps升级方案基础价为0时禁用
                    var iObj = this.otherFinanceInfo.find(item => this.isGpsIncreaseIncreasePriceCode(item.financeCode));
                    if (iObj && stringToNumber(val) === 0) {
                        //如果有配0则设置0且禁用，如果没配就不禁用也不改值，由后台校验
                        if ((yx.isPickerPrice(iObj.financeType) && iObj.financeGear.find(gear => gear.value == 0)) || yx.isInputPrice(iObj.financeType)) {
                            iObj.financePrice = '0';
                            this.gpsIncreaseIncreasePriceDisabled = true;
                        } else {
                            this.gpsIncreaseIncreasePriceDisabled = false;
                        }
                    } else if (iObj && stringToNumber(val) !== 0) {
                        this.gpsIncreaseIncreasePriceDisabled = false;
                    }
                }
            },

            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                if (!this.checkFieldIsChange('itemInfo.' + handleField)) return false;
                let arr = handleField.split('.');
                //其他融资项中下拉切换值
                if (arr[0] == 'otherFinanceInfo') {
                    this.handleGpsIncreaseBasePriceChange(arr[1]);
                }

                this.$nextTick(() => {
                    this.cacheData('itemInfo');
                    this.todoRecount();
                })
            },
            //下拉框改变值
            handleChange(val, handleField) {
                if (!this.checkFieldIsChange('itemInfo.' + handleField)) return false;
                let arr = handleField.split('.');
                //切换盗抢责任险加隔期限时更新基础价
                if (handleField == 'theftInsuranceInfo.increaseFinancePeriod' || handleField == 'theftInsuranceInfo.isFinanceInsurance') {
                    this.updateTheftBasePrice();
                }
                if(handleField === 'gpsInfo.gpsSupplier' && val === '5'){
                    this.theftInsuranceInfo.isGift = ''
                }
                //保险信息中所有值切换时修改逻辑
                if (arr[0] == 'insuranceInfo') {
                    this.todoInsuranceInfoSwitch();
                }
                //其他融资项中下拉切换值
                if (arr[0] == 'otherFinanceInfo') {
                    this.handleGpsIncreaseBasePriceChange(arr[1]);
                }
                if(handleField === 'theftInsuranceInfo.isFinanceInsurance'){
                    this.theftInsuranceInfo.isGift = ''
                }
                this.$nextTick(() => {
                    this.cacheData('itemInfo');
                    this.todoRecount();
                })
            },
            //贷款信息全部校验，回传给父级页
            validate(cb) {
                let refName = ['insuranceInfo', 'gpsInfo', 'theftInsuranceInfo', 'otherFinanceInfo']
                let arr = [];
                refName.forEach(ref => {
                    if (this.$refs[ref])
                        arr.push(this.$refs[ref].validate())
                })
                Promise.all(arr).then((result) => {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            }
        },
    }
</script>



