<template>
    <div>
        <div class="cm-panel" v-if="calcInfo">
            <h3 class="margin-b-14">贷款计算</h3>
            <Form ref="calcInfo" :model="calcInfo" :rules="rules.loanCalcRule" :label-width="120"
                  :show-message="true">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="车辆销售价(元)">
                            <InputNumber disabled number :value="sellingAmount" placeholder="请输入"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="首付金额(元)" prop="downPaymentAmount" :rules="newRule.downPaymentAmount">
                            <InputNumber clearable
                                         :disabled="downAmountDisabled"
                                         v-model="calcInfo.downPaymentAmount"
                                         v="calcInfo.downPaymentAmount"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="downAmountDisabled?'计算后带出':'请输入'"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="首付比例(%)" prop="downPaymentProportion" :rules="newRule.downPaymentProportion">
                            <InputNumber clearable
                                         v-model="calcInfo.downPaymentProportion"
                                         :disabled="downPctDisabled"
                                         v="calcInfo.downPaymentProportion"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="downPctDisabled?'计算后带出':'请输入'"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="尾付金额(元)" prop="finalPaymentAmount" :rules="newRule.finalPaymentAmount">
                            <InputNumber :clearable="!finalAmountDisabled"
                                         :disabled="finalAmountDisabled"
                                         v-model="calcInfo.finalPaymentAmount"
                                         v="calcInfo.finalPaymentAmount"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="finalAmountDisabled?'计算后带出':'请输入'"></InputNumber>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="尾付比例(%)" prop="finalPaymentProportion" :rules="newRule.finalPaymentProportion">
                            <InputNumber :clearable="!finalPctDisabled"
                                         :disabled="finalPctDisabled"
                                         v-model="calcInfo.finalPaymentProportion"
                                         v="calcInfo.finalPaymentProportion"
                                         @on-blur="handleBlur"
                                         @on-clear="handleBlur"
                                         :placeholder="finalPctDisabled?'计算后带出':'请输入'"></InputNumber>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**,
     * @Description: 贷款计算
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    import base from './js/loanbase';
    //floatMul乘， floatDiv除， floatAdd加，floatSub减
    import {yx, floatMul, floatDiv, floatAdd, floatSub, stringToNumber} from '@/utils/helper.js';
    import {amountValidator, pctValidator} from '@/utils/validateRules.js';

    export default {
        name: 'orderLoanCalcForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {
                tailCalcMethod: "",//尾付计算方式
            }
        },
        computed: {
            //禁用时的方法不能用yx.isEmpty，因为已经去掉了空格，这里不能去掉空格再比较
            downPctDisabled() {
                let value1 = this.calcInfo.downPaymentAmount;
                let value2 = this.calcInfo.downPaymentProportion;
                return !(value1 === '' || value1 === null) && (value2 === '' || value2 === null)
            },
            downAmountDisabled() {
                let value1 = this.calcInfo.downPaymentAmount;
                let value2 = this.calcInfo.downPaymentProportion;
                return (value1 === '' || value1 === null) && !(value2 === '' || value2 === null)
            },
            finalPctDisabled() {
                if (this.tailCalcMethod != '3') return true;
                return false;
                // let value1 = this.calcInfo.finalPaymentAmount;
                // let value2 = this.calcInfo.finalPaymentProportion;
                // return !(value1 === '' || value1 === null) && (value2 === '' || value2 === null)
            },
            finalAmountDisabled() {
                if (this.tailCalcMethod != '3') return true;
                return false;
                // let value1 = this.calcInfo.finalPaymentAmount;
                // let value2 = this.calcInfo.finalPaymentProportion;
                // return (value1 === '' || value1 === null) && !(value2 === '' || value2 === null)
            },
            newRule() {
                return {
                    "downPaymentAmount": [
                        {
                            validator:
                                (rule, value, callback) => {
                                    var num1 = value;
                                    var num2 = this.calcInfo.downPaymentProportion;
                                    if (yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                        callback(new Error('首付金额和首付比例必须填一项'));
                                    } else if (yx.isEmpty(num1) && !yx.isEmpty(num2)) {
                                        callback();
                                    } else if (!(yx.amountReg.test(value))) {
                                        callback(new Error('首付金额格式不对'));
                                    } else if (value > this.sellingAmount) {
                                        callback(new Error('首付金额不得大于销售价'));
                                    } else {
                                        callback();
                                    }
                                },
                            trigger: 'change'
                        }
                    ],
                    "downPaymentProportion": [
                        {
                            validator: (rule, value, callback) => {
                                var num1 = this.calcInfo.downPaymentAmount;
                                var num2 = value;
                                if (yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                    callback(new Error('首付金额和首付比例必须填一项'));
                                } else if (!yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                    callback();
                                } else if (!(yx.pctReg.test(value))) {
                                    callback(new Error('首付比例只能输入0 - 100'));
                                } else {
                                    callback();
                                }
                            },
                            trigger: 'change',
                        }
                    ],
                    "finalPaymentAmount": [
                        /*amountValidator('尾付金额', false),
                        {
                            validator:
                                (rule, value, callback) => {
                                    if (value > this.sellingAmount) {
                                        callback(new Error('尾付金额不得大于销售价'));
                                    } else {
                                        callback();
                                    }
                                },
                            trigger: 'change'
                        },*/
                        {
                            validator:
                                (rule, value, callback) => {
                                    var num1 = value;
                                    var num2 = this.calcInfo.finalPaymentProportion;
                                    if (this.tailCalcMethod == '3' && yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                        callback(new Error('尾付金额和尾付比例必须填一项'));
                                    } else if ((this.tailCalcMethod == '1' || this.tailCalcMethod == '3') && yx.isEmpty(num1) && !yx.isEmpty(num2)) {
                                        callback();
                                    } else if (!(yx.amountReg.test(value))) {
                                        callback(new Error('尾付金额格式不对'));
                                    } else if (value > this.sellingAmount) {
                                        callback(new Error('尾付金额不得大于销售价'));
                                    } else {
                                        callback();
                                    }
                                },
                            trigger: 'change'
                        },
                    ],
                    "finalPaymentProportion": [
                        // pctValidator('尾付比例', false),
                        {
                            validator: (rule, value, callback) => {
                                var num1 = this.calcInfo.finalPaymentAmount;
                                var num2 = value;
                                if (this.tailCalcMethod == '3' && yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                    callback(new Error('尾付金额和尾付比例必须填一项'));
                                } else if ((this.tailCalcMethod == '2' || this.tailCalcMethod == '3') && !yx.isEmpty(num1) && yx.isEmpty(num2)) {
                                    callback();
                                } else if (!(yx.pctReg.test(value))) {
                                    callback(new Error('尾付比例只能输入0 - 100'));
                                } else {
                                    callback();
                                }
                            },
                            trigger: 'change',
                        }
                    ]
                }
            }
        },
        mounted() {
            this.queryInitData('calcInfo', () => {
                //尾付计算方式 1金额2比例3自定义
                this.tailCalcMethod = yx.getTailCalcMethod(this.calcInfo.finalPaymentType)
                //初始如果是比例或金额时且销售价有值时先计算一次
                if ((this.tailCalcMethod == '1' || this.tailCalcMethod == '2') && this.sellingAmount !== '') {
                    this.updateCalcAmount(this.tailCalcMethod);
                }
                //统计一次
                this.$nextTick(() => {
                    this.todoRecount();
                })
            })
        },
        methods: {
            ...mapMutations('apply', []),
            //输入框失去焦点
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                //判断值是否改变
                if (!this.checkFieldIsChange(handleField)) return false;

                //注意：尾付可以直接按车款计算，首付不行，暂时只将尾付进行计算
                //根据首付金额清空首付比例
                if (handleField == 'calcInfo.downPaymentAmount') {
                    let value1 = this.calcInfo.downPaymentAmount;
                    let value2 = this.calcInfo.downPaymentProportion;
                    if (!yx.isEmpty(value1) && !yx.isEmpty(value2)) {
                        this.calcInfo.downPaymentProportion = "";
                    }
                    this.$refs['calcInfo'].validateField('downPaymentProportion')
                }
                //根据首付比例清空首付金额
                if (handleField == 'calcInfo.downPaymentProportion') {
                    let value1 = this.calcInfo.downPaymentAmount;
                    let value2 = this.calcInfo.downPaymentProportion;
                    if (!yx.isEmpty(value1) && !yx.isEmpty(value2)) {
                        this.calcInfo.downPaymentAmount = "";
                    }
                    this.$refs['calcInfo'].validateField('downPaymentAmount');
                }
                //自定义根据尾付金额算出尾付比例
                if (handleField == 'calcInfo.finalPaymentAmount' && this.tailCalcMethod == '3') {
                    this.updateCalcAmount(1);//根据金额算比例
                    this.$refs['calcInfo'].validateField('finalPaymentAmount');
                    this.$refs['calcInfo'].validateField('finalPaymentProportion');
                }
                //自定义根据尾付比例算出尾付金额
                if (handleField == 'calcInfo.finalPaymentProportion' && this.tailCalcMethod == '3') {
                    this.updateCalcAmount(2);//根据比例算金额
                    this.$refs['calcInfo'].validateField('finalPaymentAmount');
                    this.$refs['calcInfo'].validateField('finalPaymentProportion');
                }
                //固定金额 根据尾付金额算出尾付比例
                if (handleField == 'calcInfo.finalPaymentAmount' && this.tailCalcMethod == '1') {
                    this.updateCalcAmount(1);
                }
                //固定比例根据尾付比例算出尾付金额
                if (handleField == 'calcInfo.finalPaymentProportion' && this.tailCalcMethod == '2') {
                    this.updateCalcAmount(2);
                }

                this.$nextTick(() => {
                    this.cacheData('calcInfo');
                    this.todoRecount();
                })
            },
            //销售价改变后重新计算首尾付金额，应该在销售价blur之后再重新计算，不应该监控值   APP不计算，暂时也不计算
            updateCalcAmount(type) {
                var priceChange = false;
                if (!type) priceChange = true;
                //如果是销售金额改变并试算过的话，两个首付都有值时清空比例--改为价格改变时，首付金额和比例如果都有值时时都清空
                let value1 = this.calcInfo.downPaymentAmount;
                let value2 = this.calcInfo.downPaymentProportion;
                if (priceChange && !yx.isEmpty(value1) && !yx.isEmpty(value2)) {
                    this.calcInfo.downPaymentAmount = '';
                    this.calcInfo.downPaymentProportion = '';
                }
                if (priceChange && this.tailCalcMethod == '3') {
                    this.calcInfo.finalPaymentAmount = '';
                    this.calcInfo.finalPaymentProportion = '';
                }
                //根据尾付类型判断计算
                //尾付为固定金额时：根据尾付金额算出尾付比例
                if (this.tailCalcMethod == '1' || type == 1) {
                    let price = stringToNumber(this.calcInfo.finalPaymentAmount) || 0;
                    if (this.sellingAmount == 0 || price == 0) {
                        this.calcInfo.finalPaymentProportion = '0';
                    } else {
                        let num = floatDiv(floatMul(price, 100), this.sellingAmount);
                        this.calcInfo.finalPaymentProportion = parseFloat(num.toFixed(2));
                    }
                }
                //尾付为固定比例时：根据尾付比例算出尾付金额
                if (this.tailCalcMethod == '2' || type == 2) {
                    let ratio = stringToNumber(this.calcInfo.finalPaymentProportion) || 0;
                    if (this.sellingAmount == 0 || ratio == 0) {
                        this.calcInfo.finalPaymentAmount = '0';
                    } else {
                        let num = floatMul(floatDiv(ratio, 100), this.sellingAmount);
                        this.calcInfo.finalPaymentAmount = parseFloat(num.toFixed(2));
                    }
                }
                this.$nextTick(() => {
                    if (priceChange && !yx.isEmpty(value1))
                        this.$refs['calcInfo'].validateField('downPaymentAmount');
                    if (priceChange && !yx.isEmpty(value2))
                        this.$refs['calcInfo'].validateField('downPaymentProportion');
                    if (priceChange && !yx.isEmpty(this.calcInfo.finalPaymentProportion))
                        this.$refs['calcInfo'].validateField('finalPaymentProportion');
                    if (priceChange && !yx.isEmpty(this.calcInfo.finalPaymentAmount))
                        this.$refs['calcInfo'].validateField('finalPaymentAmount');

                })
            },

            //校验当前页，可由父页面调用
            validate(cb) {
                if (this.$refs['calcInfo']) this.$refs['calcInfo'].validate((valid) => {
                    cb && cb(valid);
                });
                else cb && cb(true);
            }
        },
    }
</script>



