<template>
    <div class="insurance-drop-panel">
        <div class="insurance-drop-header" @click="handleCheckAll">
            <Checkbox :indeterminate="indeterminate" :value="checkAll"></Checkbox>
            <span class="insurance-drop-alltext">全选</span>
        </div>
        <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange" style="height:128px;overflow:auto">
            <label class="ivu-select-item" style="display:block;" v-for="(item,index) in classDict"
                   :key="index">
                <Checkbox :label="item.dictCode" :disabled="item.disabled">{{item.dictName}}</Checkbox>
            </label>
        </CheckboxGroup>
    </div>
</template>
<script>
    /**
     * @Description: 险别下拉组件 -商业险和交强车船险，每年编码不一样
     * @Props: {index: Number}  保险项在数组中索引
     * @Props: {itemdata: Object} 保险项的数据
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'
    import {yx} from '@/utils/helper';
    //险别三年对应融资项编码，不是字典码
    const classArr = yx.classArr;

    export default {
        name: 'insuranceClassDrop',
        props: {
            index: {
                default: 0
            },
            itemdata: {}
        },
        components: {},
        data() {
            return {
                indeterminate: true,
                checkAll: false,
                checkAllGroup: [],
                classDict: [],
                financeCodeConfig: [],
                isRequireFinanceCode: [],
            }
        },
        computed: {
            ...mapState('apply', [
                'orderData',
            ]),
            //根据对应的保险项
            obj() {
                return this.itemdata
            },
            //计算文字
            checkAllGroupText() {
                let vals = this.checkAllGroup;
                let arr = [];
                vals.forEach((val) => {
                    let obj = this.classDict.find(item => item.dictCode == val);
                    if (obj) arr.push(obj.dictName);
                })
                return arr.join('，');
            }
        },
        mounted() {
            this.financeCodeConfig = this.itemdata.financeCodeConfigArr;
            this.isRequireFinanceCode = this.itemdata.isRequireFinanceCodeArr;
            let arr = classArr[parseInt(this.itemdata.period) - 1];//当年的字典
            this.isRequireFinanceCode.forEach(code => {
                if (this.itemdata.financeCode.indexOf(code) < 0) {
                    this.itemdata.financeCode.push(code);
                }
            })
            this.classDict = _.map(arr, item => {
                item.disabled = (this.financeCodeConfig.indexOf(item.dictCode) == -1) ||
                    (this.isRequireFinanceCode.indexOf(item.dictCode) > -1);
                return item;
            })
            this.checkAllGroup = this.itemdata.financeCode;
            this.checkAllGroupChange(this.checkAllGroup, true);
        },
        methods: {
            //切换全选
            checkAllGroupChange(data, first) {
                let validArr = classArr[parseInt(this.itemdata.period) - 1].filter(item => item.disabled == false)
                if (data.length > 0 && data.length === validArr.length) {
                    this.indeterminate = false;
                    this.checkAll = true;
                } else if (data.length > 0) {
                    this.indeterminate = true;
                    this.checkAll = false;
                } else {
                    this.indeterminate = false;
                    this.checkAll = false;
                }
                if (!first) this.change();
            },
            //全选
            handleCheckAll(i) {
                if (this.indeterminate) {
                    this.checkAll = false;
                } else {
                    this.checkAll = !this.checkAll;
                }
                this.indeterminate = false;

                if (this.checkAll) {
                    var arr = this.classDict.filter((item) => {
                        return (!item.disabled || this.isRequireFinanceCode.indexOf(item.dictCode) > -1)
                    })
                    this.checkAllGroup = arr.map((item) => item.dictCode)
                } else {
                    var arr = this.classDict.filter((item) => {
                        return (this.isRequireFinanceCode.indexOf(item.dictCode) > -1)
                    })
                    this.checkAllGroup = arr.map((item) => item.dictCode)
                }
                this.change()
            },
            close() {
                this.$emit('on-close')
            },
            //险别改变选择时，赋值给有效字段
            change() {
                this.$nextTick(() => {
                    this.$emit('on-change', {
                        i: this.index,
                        financeCode: this.checkAllGroup,
                        financeCodeText: this.checkAllGroupText,
                    })
                })
            }
        },
    }
</script>



