import {validators, required, pctValidator, regionValidator, amountValidator} from "@/utils/validateRules";
import {yx, stringToNumber} from '@/utils/helper.js';
//金额
export const amountRequireValidator = (text, level) => {
    let valid = (rule, value, callback) => {
        if (yx.isEmpty(value)) {
            callback(new Error(text + '不得为空'));
        } else if (/^((\d+\d*)|(\d+\d*\.\d{1,2}))$/.test(value)) {
            callback();
        } else {
            callback(new Error(text + '填写错误'));
        }
    }
    return {
        validator: valid,
        trigger: 'change'
    }
}
export default {
    //TODO: 贷款基本信息表单
    loanBasicRule: {
        "businessType": [required('业务类型')], //业务类型
        "leaseType": [required('租赁属性')], //租赁属性
        "repaymentMode": [required('还款方式')], //还款方式
        "repaymentFrequency": [required('还款频率')], //还款频率
        "financePeriod": [required('融资期限')], //融资期限
        "purchasePurpose": [required('购车目的')], // 购车目的
        "carCommercialType": [required('车辆使用性质')], //车辆使用性质
        // "carGuidancePrice": [required('新车指导价')], //新车指导价
        "actualSellingPrice": [
            //{required: true, type: 'number', message: '实际销售价不能为空', trigger: 'change'}
        ], //实际销售价
        "isResourceCar": [required('是否资源车')], // 是否资源车
        "carType": [required('车辆类型')], //车辆类型  -->判断   二手车+7个
        "mileage": [
            {
                validator: (rule, value, callback) => {
                    if (yx.isEmpty(value)) {
                        callback(new Error('公里数不能为空'));
                    } else if (/^([1-5]\d|\d)(\.\d{1,2})?$|^60(\.[0]{1,2})?$/.test(value)) {
                        if (stringToNumber(value) == 0)
                            callback(new Error('公里数必须大于0！'));
                        else callback();
                    } else {
                        callback(new Error('公里数只能输入0 - 60'));
                    }
                },
                trigger: 'change'
            }
            //{required: true, type: 'number', message: '公里数不能为空', trigger: 'change'}
        ], //公里数（万公里）----二手车
        "carEstimatedPrice": [
            //amountRequireValidator('车辆预估价'),
            //{required: true, type: 'number', message: '车辆预估价不能为空', trigger: 'change'}
        ], //车辆预估价      ----二手车
        "vinNum": [required('车架号（VIN码）'), {
            validator: validators.isVehicleVin,
            trigger: 'change'
        }], //车架号（VIN码） ----二手车  文档字段名：车架VIN号码？
        "productionDate": [], //出厂日期        ----二手车
        "initialDate": [], //初登日期        ----二手车
        "provinceCode": [required('')], //当前牌照第一位        ----二手车
        "cityCode": [required('')], //当前牌照第一位第二位        ----二手车
        "licenseSequence": [required(''), {
            validator: validators.licenseSequence,
            trigger: 'change'
        }], //当前牌照尾数        ----二手车
        //"licenseNum": [required('当前牌照')], //当前牌照        ----二手车 牌照完整组合字段
        "carEvaluationAgency": [required('二手车评估机构')], //二手车评估机构  ----二手车
        "isReverseMortgage": [required('是否反贷')], //是否反贷  -->判断
        "carInvoiceDate": [
            {
                validator: (rule, value, callback) => {

                    let filed = '车辆开票日期';
                    if (value == '') {
                        callback(new Error(filed + '不能为空'));
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + '格式不对'));
                    }
                },
                trigger: 'change'
            }
        ], //车辆开票日期   ----新车 && 反贷
        "carTransferDate": [
            {
                validator: (rule, value, callback) => {
                    let filed = '车辆过户完成日期';
                    if (value == '') {
                        callback(new Error(filed + '不能为空'));
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + '格式不对'));
                    }
                },
                trigger: 'change'
            }
        ], //车辆过户完成日期----二手车 && 反贷
        //新增旁边段
        plate_Region: [validators.regionValidator('预计上牌', 2)],
        "custPrimaryChannel": [required('客户一级渠道')],
        //新增车抵贷时：车辆过户次数和交强险失效日
        "transferTimes": [
            {
                validator: (rule, value, callback) => {
                    if (yx.isEmpty(value)) {
                        callback(new Error('车辆过户次数不能为空'));
                    } else if (/^([1-9]\d|\d)$|^100$/.test(value)) {
                        callback();
                    } else {
                        callback(new Error('车辆过户次数只能输入0 - 100'));
                    }
                },
                trigger: 'change'
            }
        ],
        "compulsoryInsuranceExpiryDate": [
            {
                validator: (rule, value, callback) => {
                    let filed = '交强险失效日';
                    if (value == '') {
                        callback(new Error(filed + '不能为空'));
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + '格式不对'));
                    }
                },
                trigger: 'change'
            }
        ],
    },
    //TODO: 贷款保险信息表单
    loanInsuranceRule: {
        "isFinanceInsurance": [required('是否融保险')],
        "insuranceChannel": [required('保险出单地')],
        "insuranceMode": [required('商业险出单方式')],
        "itemRule": {
            "insuranceKind": [{
                type: 'array',
                required: true,
                message: '险种不能为空',
                trigger: 'change'
            }],
            "insuranceAmount": [
                {required: true, type: 'number', message: '融资额不能为空', trigger: 'change'}
            ],
        }
    },
    //TODO: 贷款GPS信息表单
    loanGpsRule: {
        "custPrimaryChannel": [required('客户一级渠道')],
        "gpsSupplier": [required('GPS厂商')],
        "gpsPriceType": [required('GPS价格类型')],
        "gpsBasePrice": [
            {required: true, type: 'number', message: 'GPS基础价金额不能为空', trigger: 'change'}
        ],
        "gpsIncreasePrice": [
            {required: true, type: 'number', message: 'GPS加价金额不能为空', trigger: 'change'}
        ],
        "increaseFinanceType": [required('GPS加价放款类型')],
    },
    //TODO: 贷款盗抢责任信息表单
    loanTheftRule: {
        "buyTheftInsurance": [required('是否融盗抢责任险')],
        "increaseFinancePeriod": [required('盗抢责任险加融期限')],
        "increasePriceType": [required('盗抢责任险加价类型')],
        "isGift": [required('是否赠送')],
    },
    //TODO: 贷款其他融资项信息表单   应该在页面中动态生成校验
    loanOtherRule: {
        "financeSelected": [required('融资项档次')],
        "financeAmount": [
            {required: true, type: 'number', message: '融资项金额不能为空', trigger: 'change'}
        ]
    },
    //TODO: 贷款贴手续费信息表单
    feeDiscountInfoRule: {},
    //TODO: 贷款贴息信息表单
    loanTiexiRule: {
        "isDiscount": [required('是否贴息')],
    },
    //TODO: 贷款保证金信息表单
    loanDepositRule: {
        "marginType": [required('保证金设定')],
        "marginAmount": [],
        "isOffset": [required('保证金是否冲抵')],
    },
    //TODO: 贷款计算信息表单
    loanCalcRule: {},
}



