import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

import regionPicker from "@/components/apply/regionPicker.vue";
import insuranceClassDrop from '../insuranceClassDrop.vue';
import insuranceDrop from '../insuranceDrop.vue';

import {getBaseField, getLoanFieldNum} from './loanFields.js';
import TRANS from './trans.js';
import rules from './loanRules.js'
import {Clickoutside} from '@/utils/clickoutside';
import {yx, stringToNumber} from '@/utils/helper';
import {amountValidator} from '@/utils/validateRules';

export default {
    directives: {
        Clickoutside
    },
    components: {
        regionPicker,
        insuranceClassDrop,
        insuranceDrop
    },
    data() {
        return {
            rules: rules,
            baseInfo: undefined,
            itemInfo: undefined,//包括下面四项
            insuranceInfo: undefined,
            gpsInfo: undefined,
            theftInsuranceInfo: undefined,
            otherFinanceInfo: undefined,
            discountInfo: undefined,
            feeDiscountInfo: {isDiscount: '0'},
            rateDiscountInfo: {isDiscount: '0'},
            marginInfo: undefined,
            calcInfo: undefined,
            orderNo: this.$route.params.orderNo,
            gpsFactoryDictList: [],
            carEvaluationAgencyDictList: [],
        }
    },
    computed: {
        ...mapState('apply', [
            'orderData',
            'orderTabs'
        ]),
        ...mapState({
            'dp': state => state.dict.dict
        }),
        loanNextDisabled() {
            return this.orderData.loanNextDisabled;
        },
        //是否车抵贷 0130000100000
        isChedidai() {
            return this.baseInfo && yx.isChedidai(this.baseInfo.businessType);
        },
        //是否二手车
        isSecondhand() {
            return this.baseInfo && yx.isSecondCar(this.baseInfo.carType);
        },
        //是否反贷 0否 1是
        isReverseMortgage() {
            return this.baseInfo && yx.isAntiLoan(this.baseInfo.isReverseMortgage);
        },
        //融资期限转换为年份
        financePeriodYear() {
            return yx.calcFinanceYear(this.baseInfo);
        },
        //显示当前页新车销售价或二手车指导价
        sellingAmount() {
            let baseInfo = this.orderData.loan.baseInfo;
            let price = this.isSecondhand ? baseInfo.carEstimatedPrice : baseInfo.actualSellingPrice;
            return stringToNumber(price);
        },
        //基本信息是否填写完整，未填写完整时后续全部禁用
        nextDisabled() {
            return this.financePeriodYear <= 0 || this.sellingAmount === "";
            if (!this.orderData.loan || !this.orderData.loan.baseInfo) return true;
            let obj = getBaseField(TRANS.transToSave('baseInfo', this.orderData.loan))
            return !((obj.total > 0) && (obj.total == obj.count));
        },
        //是否选择了融保险
        isBuyInsurance() {
            return this.insuranceInfo && this.insuranceInfo.isFinanceInsurance == '1';
        },
        //是否选择了融盗抢险
        isBuyTheftInsurance() {
            return this.theftInsuranceInfo && this.theftInsuranceInfo.isFinanceInsurance == '1';
        },
        //盗抢险加融期限根据
        theftPeriodDict() {
            let arr = [];
            for (let i = 1, l = this.financePeriodYear; i <= l; i++) {
                arr.push({
                    dictName: i + '年',
                    dictCode: '' + i
                })
            }
            return arr;
        },
    },
    methods: {
        ...mapMutations('app', ['showLoading', 'hideLoading']),
        ...mapMutations('apply', ['setLoanNextDisabled', 'setOrderLoanTabSaved', 'setChangeRegion', 'setLoanData', 'setOrderModuleNumber', 'setOrderModuleError']),
        ...mapActions('dict', ['queryDictData']),
        errorModal(content, onOk, okText) {
            var arr = content.split(';');
            var text = '';
            arr.forEach(item => {
                if (item) text += !!text ? ('；<br>' + item) : item;
            })
            let options = {
                title: '提示',
                okText: okText || '确 定',
                content: text,
            };
            if (onOk) options.onOk = onOk;
            this.$Modal.error(options);
        },
        //基本信息是否填写完整，未填写完整时后续全部禁用
        updateLoanNextDisabled() {
            if (!this.baseInfo) return true;
            let obj = getBaseField(TRANS.transToSave('baseInfo', this.orderData.loan))
            this.setLoanNextDisabled(obj.total != obj.count)
        },
        //更新贷款tab中的错误，数字，缓存单独触发，不一起做
        todoRecount() {
            this.updateLoanFieldsNumber();
            this.$nextTick(() => {
                let el = $('.apply-loan-module .ivu-form-item-error').length;
                this.hasError = el > 0;
                this.setOrderModuleError({index: 1, hasError: this.hasError});
            })
        },
        //生成校验条件
        createRule(obj) {
            let rule = [];
            let isNeeded = yx.isItemRequire(obj['isRequire']);
            if (yx.isPickerPrice(obj['financeType'])) {//下拉均校验必填
                rule.push({
                    validator: (rule, value, callback) => {
                        if (!(yx.amountReg.test(value)))
                            callback(new Error(obj.financeName + '不能为空'));
                        else
                            callback();
                    },
                    trigger: 'change'
                })
                return rule;
            }
            //输入框校验最大最小
            if (yx.isInputPrice(obj['financeType'])) { //输入框时校验
                let min = !yx.isEmpty(obj['financePriceMin']) ? parseFloat(obj['financePriceMin']) : 0;
                let max = !yx.isEmpty(obj['financePriceMax']) ? parseFloat(obj['financePriceMax']) : '';
                rule.push(amountValidator(obj.financeName, isNeeded, max, min));
                return rule;
            }
            if (isNeeded && yx.isRatioPrice(obj['financeType'])) {
                rule.push({
                    required: true,
                    message: obj.financeName + '不能为空',
                    trigger: 'change'
                })
                return rule;
            }
        },

        //更新贷款信息的个数
        updateLoanFieldsNumber() {
            this.orderData.loan.orderNo = this.orderNo;
            let params = getLoanFieldNum(this.orderData.loan);
            this.setOrderModuleNumber({
                index: 1,
                data: params
            })
        },
        //输入框失焦或下位change时根据field名称获取旧值和新
        getHandleFieldValue(obj, handleField) {
            if (typeof obj != 'object' || !handleField) return undefined;
            var arr = handleField.split('.');
            var o = obj;
            for (let i in arr) {
                if (typeof o == 'object') {
                    let k = arr[i]
                    if (_.isArray(o)) k = stringToNumber(k);
                    o = o[k]
                }
            }
            return o;
        },
        //对比新旧值
        checkFieldIsChange(handleField) {
            var loan = this.orderData.loan;
            var oldLoan = JSON.parse(sessionStorage.getItem('loan') || "{}")
            var val = this.getHandleFieldValue(loan, handleField),
                oldVal = this.getHandleFieldValue(oldLoan, handleField);
            val = (typeof val == 'object' && (val instanceof Date)) ? new Date(val).format('yyyy-MM-dd') : (typeof val == 'object') ? JSON.stringify(val) : '' + val;
            oldVal = (typeof oldVal == 'object' && (oldVal instanceof Date)) ? new Date(oldVal).format('yyyy-MM-dd') : (typeof oldVal == 'object') ? JSON.stringify(oldVal) : '' + oldVal;
            if (val === oldVal) {
                return false;
            }
            return true;
        },
        //缓存信息，只有baseInfo才会传handleField
        cacheData(bs, handleField) {
            this.setOrderLoanTabSaved(false);//如果触发缓存代表数据有变化

            //缓存当前数据，用于字段值的比较
            var sessionLoan = JSON.parse(JSON.stringify(this.orderData.loan || {}))
            sessionLoan[bs] = TRANS.transToSave(bs, this.orderData.loan);
            sessionStorage.setItem('loan', JSON.stringify(sessionLoan));

            if (this.orderData.hasSaved.loan || this.orderData.hasSaved[bs]) return false;
            const cacheKey = {
                baseInfo: ':finance:baseInfo',
                itemInfo: ':finance:financeItem',
                discountInfo: ':finance:discount',
                marginInfo: ':finance:margin',
                calcInfo: ':finance:calculation',
            }
            let key = this.orderNo + cacheKey[bs];
            this.orderData.loan.orderNo = this.orderNo;
            let row = TRANS.transToSave(bs, this.orderData.loan);
            if (!!row) {
                row.orderNo = this.orderNo;
                if (bs == 'calcInfo') {
                    row.actualSellingPrice = this.sellingAmount;
                }
            }
            let params = {
                key: key,
                value: !!row ? JSON.stringify(row) : ""
            }
            //基本信息中融资期限和价格影响到其他项，所以同时缓存，贴息会单独请求，所以不在其中
            if (bs == 'baseInfo') {
                let multiparams = [params];
                //修改融资期限影响到保险年和盗抢险
                if (handleField == 'baseInfo.financePeriod') {
                    let itemSaveInfo = TRANS.transToSave('itemInfo', this.orderData.loan);
                    multiparams.push({
                        key: this.orderNo + cacheKey['itemInfo'],
                        value: itemSaveInfo ? JSON.stringify(itemSaveInfo) : ""
                    })
                }
                //修改车款影响到计算
                if (handleField == 'baseInfo.actualSellingPrice' || handleField == 'baseInfo.carEstimatedPrice') {
                    let marginSaveInfo = TRANS.transToSave('marginInfo', this.orderData.loan);
                    multiparams.push({
                        key: this.orderNo + cacheKey['marginInfo'],
                        value: marginSaveInfo ? JSON.stringify(marginSaveInfo) : ""
                    })
                    let calcSaveInfo = TRANS.transToSave('calcInfo', this.orderData.loan);
                    multiparams.push({
                        key: this.orderNo + cacheKey['calcInfo'],
                        value: calcSaveInfo ? JSON.stringify(calcSaveInfo) : ""
                    })
                }
                this.$http.post(polarisserver + '/cache/insertMultiCache', multiparams).then(res => {
                }).catch(err => {
                    this.errorCatchModal('缓存请求出错', err)
                })
            }
            else if (bs == 'itemInfo' || bs == 'marginInfo' || bs == 'discountInfo' || bs == 'calcInfo')
                this.$http.post(polarisserver + '/cache/insertCache', params).then(res => {
                }).catch(err => {
                    this.errorCatchModal('缓存请求出错', err)
                })
        },
        //转换保存需要的数据并返回
        getSaveData() {
            this.orderData.loan.orderNo = this.orderNo;
            //获取basic数据
            const financeBaseInfo = TRANS.transToSave('baseInfo', this.orderData.loan);
            //获取融资项数据
            const financeItemInfo = TRANS.transToSave('itemInfo', this.orderData.loan);
            //获取贴息数据
            const financeDiscountInfo = TRANS.transToSave('discountInfo', this.orderData.loan);
            //获取保证金数据
            const financeMarginInfo = TRANS.transToSave('marginInfo', this.orderData.loan);
            //试算数据
            const financeCalculationInfo = TRANS.transToSave('calcInfo', this.orderData.loan);
            //financeCalculationInfo.financeCalculationTableList = [];
            financeCalculationInfo.actualSellingPrice = this.sellingAmount;
            var hasSaved = this.orderData.hasSaved.loan;
            return {
                orderNo: this.orderNo,
                hasSaved,
                financeBaseInfo,
                financeItemInfo,
                financeDiscountInfo,
                financeMarginInfo,
                financeCalculationInfo
            };
        },

        //查询gps厂商,需要带入提报店(店面)参数
        queryGpsFactoryDictList() {
            const dealerCode = this.orderData.orderinfo.dealerCode;
            //暂时用QD181016094取
            const url = baseserver + '/dictionary/queryList/GP:' + dealerCode;
            this.showLoading();
            this.$http.get(url).then(res => {
                this.hideLoading();
                let result = res.data;
                if (!result.success) return this.errorModal(result.errorMessage || '查询GPS厂商失败');
                this.gpsFactoryDictList = result.data || [];
                if (!this.gpsFactoryDictList.length) return this.errorModal('查询GPS厂商无数据');
            }).catch(err => {
                this.hideLoading();
                this.errorCatchModal('查询GPS厂商出错', err)
            })
        },
        //查询二手车评估机构,需要带入提报店(店面)参数
        queryEvalAgencyDictList() {
            const dealerCode = this.orderData.orderinfo.dealerCode;
            //暂时用QD181016094取
            const url = baseserver + '/dictionary/queryList/CE:' + dealerCode;
            this.$http.get(url).then(res => {
                let result = res.data;
                if (!result.success) return this.errorModal('查询二手车评估机构失败');
                this.carEvaluationAgencyDictList = result.data || [];
                if (!this.carEvaluationAgencyDictList.length) return this.errorModal('查询二手车评估机构无数据');
            }).catch(err => {
                this.errorCatchModal('查询二手车评估机构出错:', err)
            })
        },

        //初始化数据  基本信息basiInfo要在loan模块入口先请求，贴息在discountInfo模块里面请求，因为后面要根据融资期限刷新
        queryInitData(bs, cb) {
            var orderNo = '/' + this.$route.params.orderNo;
            var urls = {
                'baseInfo': '/finance/query/baseInfo',
                'itemInfo': '/finance/query/financeItemInfo',
                //'discountInfo': '/finance/query/financeDiscountInfo',
                'marginInfo': '/finance/query/financeMarginInfo',
                'calcInfo': '/finance/query/financeCalculationInfo',
            };
            var url = polarisserver + urls[bs] + `${orderNo}`;
            this.showLoading();
            this.$http.get(url).then(res => {
                this.hideLoading();
                if (!res.data.success) return this.errorModal(res.data.errorMessage || bs + '信息请求失败')
                var data;
                switch (bs) {
                    case 'baseInfo':
                        data = res.data.data;
                        this.baseInfo = TRANS.transToForm(bs, data);
                        break;
                    case 'itemInfo':
                        data = res.data.data;
                        if (data) {
                            this.itemInfo = TRANS.transToForm(bs, data);
                            //设置保险数据
                            this.insuranceInfo = this.itemInfo.insuranceInfo;

                            //设置GPS数据
                            this.gpsInfo = this.itemInfo.gpsInfo;
                            if (this.gpsInfo) {//查询gps厂商
                                this.queryGpsFactoryDictList();
                            }
                            //设置盗抢险数据
                            this.theftInsuranceInfo = this.itemInfo.theftInsuranceInfo;
                            //设置其他融资项数据
                            this.otherFinanceInfo = this.itemInfo.otherFinanceInfo;
                        }
                    case 'marginInfo':
                        data = res.data.data;
                        this.marginInfo = TRANS.transToForm(bs, data);
                        break;
                    case 'calcInfo':
                        data = res.data.data;
                        this.calcInfo = TRANS.transToForm(bs, data);
                        break;
                }
                this.setLoanData({
                    name: bs,
                    data: this[bs]
                })
                cb && cb(this[bs]);
            }).catch(err => {
                this.hideLoading();
                this.errorCatchModal('贷款' + bs + '信息请求出错：', err)
            });
        }
    },
    mounted() {
        //在loan主页面已经请求了贷款基本信息，在这里拿到
        this.baseInfo = this.orderData.baseInfo;
    }
}



